import React, {useEffect, useState} from 'react';
import {Breadcrumb, Button, message, Space, Table, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {SettingOutlined} from "@ant-design/icons";
// @ts-ignore
import Highlighter from "react-highlight-words"
import "../../App.css";
import {IDict} from "../../models/IDict";
import {dictAPI} from "../../services/DictService";
import {NormfactorModule} from "./NormfactorModule";
import {GridSettingsModal} from "../../components/GridSettingsModal";
import {DictModal} from "../../components/DictModal";

const {Text} = Typography;

export interface DataType {
    id: number;
    key: React.Key;
    name: string;
    ooo_code: string;
    pao_code: string;
    short_name: string;
    filials: string[];
}

export const DictModule: React.FC = () => {
    // Create Toasts context
    const [messageApi, contextHolder] = message.useMessage();
    const successUpdatedSettings = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successLoadTDicts = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorLoadTDicts = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const successCreatedDictMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorCreatedDictMessage = () => {
        messageApi.open({
            type: 'error',
            content: '     ',
            duration: 2,
        });
    }
    const errorUpdatedDictMessage = () => {
        messageApi.open({
            type: 'error',
            content: '  ',
            duration: 1,
        });
    }
    const successUpdatedDictMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const emptyFieldsMessage = () => {
        messageApi.open({
            type: 'warning',
            content: '   ',
            duration: 1,
        });
    }
    //End of toasts
    // Requests
    const [getDicts, {
        data: dicts,
        error: getDictsRequestError,
        isLoading: getDictsRequestIsLoading
    }] = dictAPI.useGetDictsMutation();
    // End of requests
    const [data, setData] = useState<DataType[]>();
    const [visibleSettings, setVisibleSettings] = useState<boolean>(false);
    const [visible, setVisible] = useState<boolean>(false);
    const [showHeader, setShowHeader] = useState<boolean>(localStorage.getItem('tableHeader-dicts') ? localStorage.getItem('tableHeader-dicts') === 'true' : true);
    const [showCellBorders, setShowBorders] = useState<boolean>(localStorage.getItem('tableBorders-dicts') ? localStorage.getItem('tableBorders-dicts') === 'true' : true);
    const [selectedDictId, setSelectedDictId] = useState<number | undefined>(undefined);
    const [selectedDictName, setSelectedDictName] = useState<string | undefined>(undefined);
    const [rowHeight, setRowHeight] = useState<string>('6vh');
    const [titleFontSize, ] = useState('13px');
    const [tableCellFontSize, ] = useState('15px');
    const [selectedRecordData, setSelectedRecordData] = useState();
    useEffect(() => {
        getDicts({})
    }, [])
    useEffect(() => {
        if (dicts) {
            setData(dicts.map((dict: IDict, key: number) => ({...dict, key} as DataType)));
            successLoadTDicts();
        }
    }, [dicts]);
    useEffect(() => {
        if (getDictsRequestError) {
            errorLoadTDicts();
        }
    }, [getDictsRequestError]);
    const columns: ColumnsType<DataType> = [
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                     </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'short_name',
            key: 'short_name',
            width: '25%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                    </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>

                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'name',
            key: 'name',
            width: '45%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                    </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>{text}</div>
                };
            },
            dataIndex: 'pao_code',
            key: 'pao_code',
            width: '8%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}> 
                    </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>{text}</div>
                };
            },
            dataIndex: 'ooo_code',
            key: 'ooo_code',
            width: '8%',
        },
        {
            title: '',
            render: (_, record) => (
                <Space size='middle'>
                    <Button onClick={() => {
                        setSelectedDictId(record.id);
                        setSelectedDictName(record.short_name);
                    }}> </Button>
                </Space>
            ),
            key: 'action',
            width: '10%',
        },
    ];
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            <Breadcrumb style={{marginBottom: 15, marginLeft: 15}}>
                <Breadcrumb.Item>
                    <a onClick={() => {
                        setSelectedDictName(undefined);
                        setSelectedDictId(undefined);
                    }}></a>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                    {selectedDictName}
                </Breadcrumb.Item>
            </Breadcrumb>
            {selectedDictId ?
                <NormfactorModule dictId={selectedDictId}/>
                :
                <>
                    {dicts &&
                    <DictModal
                        availableDicts={dicts}
                        data={selectedRecordData}
                        setData={setSelectedRecordData}
                        visible={visible}
                        setVisible={setVisible}
                        emptyFieldsMessage={emptyFieldsMessage}
                        getDicts={getDicts}
                        successCreatedDictMessage={successCreatedDictMessage}
                        errorCreatedDictMessage={errorCreatedDictMessage}
                        errorUpdatedDictMessage={errorUpdatedDictMessage}
                        successUpdatedDictMessage={successUpdatedDictMessage}
                    />
                    }
                    <GridSettingsModal
                        moduleName={'dicts'}
                        successUpdatedSettings={successUpdatedSettings}
                        visible={visibleSettings}
                        setVisible={setVisibleSettings}
                        showBorders={showCellBorders}
                        showHeader={showHeader}
                        setShowHeader={setShowHeader}
                        setShowBorders={setShowBorders}
                    />
                    <div style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        marginLeft: '15px',
                        marginRight: '15px',
                        marginBottom: '10px'
                    }}>
                        <div style={{display: 'flex'}}>
                            <Button onClick={() => setVisible(true)}> </Button>
                        </div>
                        <Tooltip placement={'leftTop'} title={' '}>
                            <Button className='settingsButton' icon={<SettingOutlined/>} shape={'circle'}
                                    onClick={() => setVisibleSettings(true)}/>
                        </Tooltip>
                    </div>
                    <Table
                        bordered={showCellBorders}
                        loading={getDictsRequestIsLoading}
                        showHeader={showHeader}
                        columns={columns}
                        dataSource={data}
                        pagination={{position: ["bottomRight"], defaultPageSize: 10}}
                        onRow={(record: any) => {
                            return {
                                onDoubleClick: () => {
                                    setVisible(true);
                                    setSelectedRecordData(record);
                                }
                            }
                        }}
                    />
                </>
            }
        </div>
    );
}