import {Button, Input, Modal, Select, Spin, Typography} from "antd";
import React, {useEffect, useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {userAPI} from "../services/UserService";
import {IUser} from "../models/IUser";
import {departmentAPI} from "../services/DepartmentService";
import {IStatus} from "../models/IStatus";
import {Simulate} from "react-dom/test-utils";
import change = Simulate.change;

const {Text} = Typography;
const {TextArea} = Input;

type ModalProps = {
    declineModal: { visible: boolean, status: IStatus | undefined, setStatus: Function } | null,
    setDeclineModal: Function,
    handleUpdateTep: Function,
    warningEmptyDeclineField: Function,
    setDeclineExplanationZam: Function,
    setDeclineExplanationBoss: Function,
}

export const DeclineTepModal = (props: ModalProps) => {
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const [declineExplanation, setDeclineExplanation] = useState<string>('');
    return (
        <Modal
            title={'  '}
            centered={true}
            open={props.declineModal?.visible}
            onCancel={() => props.setDeclineModal((prev: any) => {
                return {
                    ...prev,
                    visible: false
                }
            })}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={"450px"}
        >
            <div style={{display: 'flex', flexDirection: 'column'}}>
                <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'start',
                    width: '100%',
                    marginBottom: 15,
                    marginTop: 5,
                    fontSize: 14
                }}>
                    <Text style={{marginBottom: 5}}></Text>
                    <TextArea value={declineExplanation} onChange={(e) => setDeclineExplanation(e.target.value)}/>
                </div>
                <div style={{display: 'flex', justifyContent: 'space-evenly'}}>
                    <Button danger onClick={() => {
                        if (declineExplanation.trim()) {
                            props.handleUpdateTep(props.declineModal?.status, declineExplanation);
                            props.setDeclineModal((prev: any) => {
                                return {
                                    ...prev,
                                    visible: false
                                }
                            })
                            if (props.declineModal?.status?.code === "5") {
                                props.setDeclineExplanationZam(declineExplanation)
                                props.declineModal.setStatus(props.declineModal?.status?.name)
                            }
                            if (props.declineModal?.status?.code === "6") {
                                props.setDeclineExplanationBoss(declineExplanation)
                                props.declineModal.setStatus(props.declineModal?.status?.name)
                            }
                        }
                        else
                            props.warningEmptyDeclineField();
                    }}></Button>
                    <Button onClick={() => {
                        props.setDeclineModal((prev: any) => {
                            return {
                                ...prev,
                                visible: false
                            }
                        })
                    }}></Button>
                </div>
            </div>
        </Modal>
    )
}