import {Alert, Button, Input, Modal, Select, Spin, Typography} from "antd";
import {PullRequestOutlined, LoadingOutlined} from "@ant-design/icons";
import React, {useEffect, useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {normfactorAPI} from "../services/NormfactorService";

const {Text} = Typography;
const {TextArea} = Input;

interface ResponsibilityStructure {
    id: number;
    name: string;
}

type ModalProps = {
    dictId: number,
    responsibilityStructures: ResponsibilityStructure[],
    visible: boolean,
    setVisible: Function,
    emptyFieldsWarning: Function,
    getNormfactors: Function,
    successCreatedNormfactorMessage: Function,
    errorCreatedNormfactorMessage: Function,
    successUpdatedNormfactorMessage: Function,
    errorUpdatedNormfactorMessage: Function,
    data: any,
    setData: Function
}

export const NormfactorModal = (props: ModalProps) => {
    const [preCreateNormfactor, {
        data: preCreatedNormfactor,
        isLoading: preCreateNormfactorLoading
    }] = normfactorAPI.usePreCreateNormfactorMutation();
    const [createNormfactor, {
        data: createdNormfactor,
        error: createdNormfactorError,
        isLoading: createNormfactorLoading
    }] = normfactorAPI.useCreateNormfactorsMutation();
    const [updateNormfactor, {
        data: updatedNormfactor,
        error: updatedNormfactorError,
        isLoading: updateNormfactorLoading
    }] = normfactorAPI.useUpdateNormfactorsMutation();
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const [department, setDepartment] = useState<string>();
    const [nfName, setNfName] = useState<string>();
    const [dimension, setDimension] = useState<string>();
    const [source, setSource] = useState<string>();
    const [number, setNumber] = useState<string>();
    const [isNormfactorNumberExist, setIsNormfactorNumberExist] = useState<boolean>(false);
    const [isNormfactorExistInCurrentDict, setIsNormfactorExistInCurrentDict] = useState<boolean>(false);
    const handleSelectResponsibilityStructure = (value: string) => {
        setDepartment(value);
    }
    const handleUpdateDict = () => {
        if (department && nfName && dimension && source && number) {
            updateNormfactor({
                nfId: props.data.id,
                dictId: props.dictId,
                dimension,
                nfName,
                number,
                responsibilityStructure: department,
                source
            })
        } else {
            props.emptyFieldsWarning()
        }
    }
    const handleCreateDict = () => {
        if (department && nfName && dimension && source && number) {
            createNormfactor({
                dictId: props.dictId,
                dimension,
                nfName,
                number,
                responsibilityStructure: department,
                source
            })
        } else {
            props.emptyFieldsWarning()
        }
    }
    const handleEndOfNumberInput = () => {
        if (number)
            preCreateNormfactor({number, dictId: props.dictId});
    }
    useEffect(() => {
        if (!props.visible) {
            setDepartment('');
            setNfName('');
            setDimension('');
            setSource('');
            setNumber('');
            setIsNormfactorNumberExist(false);
            setIsNormfactorExistInCurrentDict(false);
            props.setData(null);
        } else if (props.data) {
            setDepartment(props.data.responsibility_structure);
            setNfName(props.data.name);
            setDimension(props.data.dimension);
            setSource(props.data.source);
            setNumber(props.data.number);
        }
    }, [props.visible])
    useEffect(() => {
        if (createdNormfactor) {
            props.successCreatedNormfactorMessage();
            props.getNormfactors(props.dictId);
            props.setVisible(false);
        }
    }, [createdNormfactor])
    useEffect(() => {
        if (updatedNormfactorError) {
            props.errorCreatedNormfactorMessage();
        }
    }, [createdNormfactorError])
    useEffect(() => {
        if (updatedNormfactor) {
            props.successUpdatedNormfactorMessage();
            props.getNormfactors(props.dictId);
            props.setVisible(false);
        }
    }, [updatedNormfactor])
    useEffect(() => {
        if (updatedNormfactorError) {
            props.errorUpdatedNormfactorMessage();
        }
    }, [updatedNormfactorError])
    useEffect(() => {
        if (preCreatedNormfactor) {
            setIsNormfactorNumberExist(preCreatedNormfactor.normfactorExist)
            if (preCreatedNormfactor.normfactorExist) {
                setNumber(preCreatedNormfactor.number);
                setNfName(preCreatedNormfactor.name);
                setDimension(preCreatedNormfactor.dimension);
                setSource(preCreatedNormfactor.source);
                setDepartment(preCreatedNormfactor.department);
                setIsNormfactorExistInCurrentDict(preCreatedNormfactor.normfactorExistInCurrentDict);
            }
        }
        else{
            setIsNormfactorExistInCurrentDict(false);
            setDepartment('');
            setNfName('');
            setDimension('');
            setSource('');
        }
    }, [preCreatedNormfactor])
    return (
        <Modal
            title={props.data ? ' ' : ' '}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={"550px"}
        >
            <div style={{display: 'flex', flexDirection: 'column'}}>
                {updateNormfactorLoading ?
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: 400}}>
                        <Spin size="large"/>
                    </div>
                    :
                    <>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Input
                                value={number}
                                onChange={(e: any) => setNumber(e.target.value)}
                                onBlur={handleEndOfNumberInput}
                                prefix={preCreateNormfactorLoading ? <LoadingOutlined/> : isNormfactorNumberExist ?
                                    <PullRequestOutlined/> : undefined}
                                status={isNormfactorNumberExist ? 'warning' : undefined}
                            />
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <TextArea disabled={isNormfactorExistInCurrentDict || isNormfactorNumberExist} style={{minHeight: 78}} value={nfName}
                                      onChange={(e: any) => setNfName(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Input disabled={isNormfactorExistInCurrentDict || isNormfactorNumberExist} value={dimension} onChange={(e: any) => setDimension(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}>  </Text>
                            <TextArea disabled={isNormfactorExistInCurrentDict || isNormfactorNumberExist} style={{minHeight: 150}} value={source}
                                      onChange={(e: any) => setSource(e.target.value)}/>

                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Select
                                disabled={isNormfactorExistInCurrentDict || isNormfactorNumberExist}
                                allowClear
                                value={department}
                                style={{width: 500}}
                                onChange={handleSelectResponsibilityStructure}
                                options={props.responsibilityStructures.map((rs: any) => ({
                                    value: rs.name,
                                    label: rs.name
                                }))}
                            />
                        </div>
                        {(isNormfactorNumberExist && !isNormfactorExistInCurrentDict) &&
                            <Alert style={{marginBottom: 15}} message={'  ,       '} type={'warning'}/>
                        }
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                             style={{
                                 display: 'flex',
                                 justifyContent: 'space-evenly',
                                 alignItems: 'center',
                                 width: '100%',
                                 fontSize: 14
                             }}>
                            {props.data ?
                                <Button disabled={preCreateNormfactorLoading}
                                        onClick={handleUpdateDict}></Button>
                                :
                                preCreatedNormfactor !== undefined ?
                                    !isNormfactorExistInCurrentDict ?
                                        <Button disabled={preCreateNormfactorLoading}
                                                onClick={handleCreateDict}>{isNormfactorNumberExist ? '  ' : ''}</Button>
                                        :
                                        <Alert message={'     '} type={'warning'}/>
                                    :
                                    <Button disabled={preCreateNormfactorLoading}
                                            onClick={handleCreateDict}></Button>
                            }
                            <Button onClick={() => props.setVisible(false)}></Button>
                        </div>
                    </>
                }
            </div>
        </Modal>
    )
}