import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {host, port, secure} from "../config/config";

export const settingsAPI = createApi({
    reducerPath: 'settingsAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api`}),
    tagTypes: ['Settings'],
    endpoints: (build) => ({
        updateSettings: build.mutation<any, { year: string, startPeriod: string, endPeriod: string }>({
            query: (post) => ({
                url: `/settings`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Settings']
        }),
    })
})