import {Button, Input, Modal, Select, Spin, Switch, Typography} from "antd";
import React, {useEffect, useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {IFilial} from "../models/IFilial";
import {IRole} from "../models/IRole";
import {useAppDispatch, useAppSelector} from "../hooks/redux";
import {userAPI} from "../services/UserService";
import {setUserInfo} from "../store/reducers/GeneralSlice";
import {IDepartment} from "../models/IDepartment";
import {departmentAPI} from "../services/DepartmentService";

const {Text} = Typography;

type ModalProps = {
    getUsers: Function,
    visible: boolean,
    setVisible: Function,
    emptyFieldsMessage: Function,
    successUpdateUserMessage: Function,
    errorUpdateUserMessage: Function,
    successCreatedUserMessage: Function,
    errorCreatedUserMessage: Function,
    data?: {
        department: any,
        filials: IFilial[],
        name: string,
        roles: IRole[],
        secondName: string,
        surname: string,
        username: string
    }
    setData: Function,
}

export const UserModal = (props: ModalProps) => {
    const generalStore = useAppSelector(store => store.userReducer);
    const dispatch = useAppDispatch();
    const {data: user, error, isLoading: isLoadingUser, refetch} = userAPI.useGetUserQuery();
    const [updateUser, {
        data: updatedUser,
        error: updateUserError,
        isLoading: updateUserLoading
    }] = userAPI.useUpdateUserMutation();
    const {
        data: departments,
        error: getDepartmentsError,
        isLoading: getDepartmentsLoading,
        refetch: refetchDepartments
    } = departmentAPI.useGetDepartmentsQuery();
    const [createUser, {
        data: createdUser,
        error: createUserError,
        isLoading: createUserLoading
    }] = userAPI.useCreateUserMutation();
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const userStore = useAppSelector(store => store.userReducer);
    const [modalWidth, setModalWidth] = useState<number>(750);
    const [username, setUsername] = useState<string>();
    const [name, setName] = useState<string>();
    const [surname, setSurname] = useState<string>();
    const [secondName, setSecondName] = useState<string>();
    const [selectedFilials, setSelectedFilials] = useState<string[]>([]);
    const [selectedRoles, setSelectedRoles] = useState<number[]>([]);
    const [selectedDepartment, setSelectedDepartment] = useState<string>();
    const handleSelectFilials = (value: string[]) => {
        setSelectedFilials(value);
    }
    const handleSelectRoles = (value: number[]) => {
        setSelectedRoles(value);
    }
    const handleSelectDepartments = (value: string) => {
        setSelectedDepartment(value);
    }
    const handleUpdateUser = () => {
        if (username && name && surname && secondName && selectedFilials && selectedRoles.length > 0 && props.data) {
            updateUser({
                // @ts-ignore
                departmentId: departments.find(dep => dep.name === selectedDepartment)?.id, //todo
                filials: selectedFilials.map(f => f).join(';'),
                //filials: userStore.filials.map((filial: IFilial) => filial.id).join(';'),
                name,
                roles: selectedRoles.map(role => role).join(';'),
                secondName,
                surname,
                username: 'sgp\\' + username,
                oldUsername: props.data.username
            })
        } else {
            props.emptyFieldsMessage()
        }
    }
    const handleCreateUser = () => {
        if (username && name && surname && secondName && selectedFilials && selectedRoles.length > 0) {
            createUser({
                departments: selectedDepartment ?? "",
                filials: selectedFilials.map(f => f).join(';'),
                name,
                roles: selectedRoles.map(role => role).join(';'),
                secondName,
                surname,
                username: 'sgp\\' + username
            })
        } else {
            props.emptyFieldsMessage()
        }
    }
    useEffect(() => {
        if (user) dispatch(setUserInfo(user));
    }, [user])
    useEffect(() => {
        if (!props.visible) {
            setUsername('');
            setName('');
            setSurname('');
            setSecondName('');
            setSelectedFilials([]);
            setSelectedRoles([]);
            setSelectedDepartment('');
            props.setData(null);
        } else {
            if (props.data) {
                setUsername(props.data.username.slice(4));
                setName(props.data.name);
                setSurname(props.data.surname);
                setSecondName(props.data.secondName);
                setSelectedFilials(props.data.filials.map((filial: IFilial) => filial.id));
                setSelectedRoles(props.data.roles.map((role: IRole) => role.code));
                setSelectedDepartment(props.data.department?.name);
            }
        }
    }, [props.visible])
    useEffect(() => {
        if (updatedUser) {
            refetch();
            props.successUpdateUserMessage();
            props.getUsers();
            props.setVisible(false);
        }
    }, [updatedUser])
    useEffect(() => {
        if (updateUserError) {
            props.errorUpdateUserMessage();
        }
    }, [updateUserError])
    useEffect(() => {
        if (selectedRoles.find((roleCode: number) => roleCode === generalStore?.roles.find((role: IRole) => role.code === 3)?.code || roleCode === generalStore?.roles.find((role: IRole) => role.code === 4)?.code)) {
            setSelectedDepartment(props.data?.department?.name);
        } else {
            console.log(selectedRoles, generalStore?.roles, 'kiiil')
            setSelectedDepartment('')
        }
    }, [selectedRoles])
    useEffect(() => {
        if (createdUser) {
            props.successCreatedUserMessage();
            props.getUsers();
            props.setVisible(false);
        }
    }, [createdUser])
    useEffect(() => {
        if (createUserError) {
            props.errorCreatedUserMessage();
        }
    }, [createUserError])
    console.log(selectedDepartment)
    return (
        <Modal
            title={props.data ? ' ' : ' '}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={modalWidth}
        >
            <div style={{display: 'flex', flexDirection: 'column'}}>
                {updateUserLoading ?
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: 500}}>
                        <Spin size="large"/>
                    </div>
                    :
                    <>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Input addonBefore={`sgp\\`} value={username}
                                   onChange={(e: any) => setUsername(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Input value={surname} onChange={(e: any) => setSurname(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Input value={name} onChange={(e: any) => setName(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Input value={secondName} onChange={(e: any) => setSecondName(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Select
                                mode={'multiple'}
                                value={selectedFilials}
                                style={{width: modalWidth - 50}}
                                onChange={handleSelectFilials}
                                options={userStore.filials.map((filial: IFilial) => ({
                                    value: filial.id,
                                    label: filial.name
                                }))}
                            />
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Select
                                allowClear
                                mode={'multiple'}
                                value={selectedRoles}
                                style={{width: modalWidth - 50}}
                                onChange={handleSelectRoles}
                                options={generalStore?.roles.map((role: IRole) => ({
                                    value: role.code,
                                    label: role.name
                                }))}
                            />
                        </div>
                        {(selectedRoles.find((role: number) => role === generalStore?.roles.find((role: IRole) => role.code === 3)?.code) ||
                                selectedRoles.find((role: number) => role === generalStore?.roles.find((role: IRole) => role.code === 4)?.code)) &&
                            <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'start',
                                width: '100%',
                                marginBottom: 15,
                                marginTop: 5,
                                fontSize: 14
                            }}>
                                <Text style={{marginBottom: 5}}></Text>
                                <Select
                                    value={selectedDepartment}
                                    style={{width: modalWidth - 50}}
                                    onChange={handleSelectDepartments}
                                    options={departments && departments.map((department: IDepartment) => ({
                                        value: department.name,
                                        label: department.name
                                    }))}
                                />
                            </div>
                        }
                        {(selectedRoles.find((role: number) => role === generalStore?.roles.find((role: IRole) => role.code === 2)?.code) && false) &&
                            <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                                display: 'flex',
                                justifyContent: 'start',
                                alignItems: 'center',
                                width: '100%',
                                marginBottom: 15,
                                marginTop: 5,
                                fontSize: 14
                            }}>
                                <Text style={{marginBottom: 5, marginRight: 15}}>   
                                     </Text>
                                <Switch style={{width: 50}}/>
                            </div>
                        }
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                             style={{display: 'flex', justifyContent: 'space-evenly', width: '100%', fontSize: 14}}>
                            {props.data ?
                                <Button onClick={handleUpdateUser}></Button>
                                :
                                <Button onClick={handleCreateUser}></Button>
                            }
                            <Button onClick={() => props.setVisible(false)}></Button>
                        </div>
                    </>
                }
            </div>
        </Modal>
    )
}