export let host: string = document.location.host.split(':')[0]
export let port: string = ''
export let secure: string = document.location.protocol.slice(0, -1);

if (document.location.host.split(':')[1]) {
    if (document.location.host.split(':')[1] !== '3000')
        port = document.location.host.split(':')[1] + '/nil_arm'
    else
        port = '8080/nil_arm'
} else
    //port = '80/nil_arm'
    port='/nil_arm'