import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {host, port, secure} from "../config/config";
import {IFilial} from "../models/IFilial";


export const filialAPI = createApi({
    reducerPath: 'filialAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/filials`}),
    tagTypes: ['Filial'],
    endpoints: (build) => ({
        getFilials: build.query<IFilial[], void>({
            query: () => ({
                url: `/get-all-filials`,
            }),
            providesTags: result => ['Filial']
        }),
    })
})
