import {Button, Divider, Modal, Radio, Switch, Typography} from "antd";
import React, {useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {GroupedColumnTepsGrid} from "../ENUMS";
import {host, port, secure} from "../config/config";
import {type} from "os";

const {Title, Text} = Typography;

type ModalProps = {
    warningLoadReport: Function,
    selectedDepartment: string,
    selectedYear: string,
    visible: boolean,
    setVisible: Function,

}

export const BeforeReportDownloadModal = (props: ModalProps) => {
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const downloadHandler = () => {
         props.warningLoadReport();
         let tmpButton = document.createElement('a')
         tmpButton.href = `${secure}://${host}:${port}/api/report/consolidatedReport?department=${props.selectedDepartment}&year=${props.selectedYear}`
         tmpButton.click();
         props.setVisible(false);
    }
    const cloudHandler = () => {
        let requestOptions = {
            method: 'GET',
            redirect: 'follow'
        };

        // @ts-ignore
        fetch("https://sco1-vapp-04/nil_arm/api/report/consolidatedReport?department=&year=2022", requestOptions)
        //fetch(`http://localhost:8080/nil_arm/api/report/consolidatedReport?department=${props.selectedDepartment}&year=2022`)
            // Retrieve its body as ReadableStream
            .then((response) => response.body)
            .then((body) => {
                // @ts-ignore
                const reader = body.getReader();

                return new ReadableStream({
                    start(controller) {
                        return pump();

                        // @ts-ignore
                        function pump() {
                            return reader.read().then(({ done, value }) => {
                                // When no more data needs to be consumed, close the stream
                                if (done) {
                                    controller.close();
                                    return;
                                }

                                // Enqueue the next data chunk into our target stream
                                controller.enqueue(value);
                                return pump();
                            });
                        }
                    },
                });
            })
            .then((stream) => new Response(stream))
            .then((response) => response.blob())
            .then((blob) => {
                let myHeaders = new Headers();
                myHeaders.append("Authorization", "Bearer W9GxZVENOgRyUZOrxk+LdaR1M9t1ntI7217KK2hOR+qxi7wdv1IpvUiS3Pgf7I0t0V1xe8D+X1q3MdwFb/v7EcgdTxzSSld9b+c2qjZ6WvfIjHaV+PUkvBPZ/8X3Jy2/Q+xlAtO4qEuZPa2OnMIFUh7shjHBnw1BFhqb0IyX6m0=");
                let formdata = new FormData();
                formdata.append("file", blob, "file.xlsx");
                let requestOptions = {
                    method: 'POST',
                    headers: myHeaders,
                    body: formdata,
                    redirect: 'follow'
                };
                // @ts-ignore
                fetch("https://r7.sgp.ru/api/2.0/files/94/upload", requestOptions)
                    .then(response => response.text())
                    .then(result => {
                        let response = JSON.parse(result);
                        let id = response.response.id;
                        window.open(`https://r7.sgp.ru/Products/Files/DocEditor.aspx?fileid=${id}`, '_blank');
                        props.setVisible(false);
                    })
                    .catch(error => console.log('error', error));
                return URL.createObjectURL(blob)
            })
            //.then((url) => console.log((image.src = url)))
            .catch((err) => console.error(err));
    }
    return (
        <Modal
            title={' '}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={"400px"}
        >
            <div style={{display: 'flex', justifyContent: 'space-between', marginTop: 30}}>
                <Button style={{width: 150}} onClick={downloadHandler}></Button>
                <Button style={{width: 150}} onClick={cloudHandler}>  </Button>
            </div>
        </Modal>
    )
}