import {BrowserRouter, Route, Routes} from 'react-router-dom'
import React from "react";
import {MainScreen} from '../Pages/MainScreen';

export const Router: React.FC = () => {
    return (<BrowserRouter>
        <Routes>
            <Route
                path='/nil_arm'
                element={<MainScreen/>}
            />
        </Routes>
    </BrowserRouter>)
};