import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {IUser} from "../models/IUser";
import {host, port, secure} from "../config/config";

interface IUpdateUserRequest {
    username: string;
    oldUsername: string;
    name: string;
    surname: string;
    secondName: string;
    roles: string;
    filials: string;
    departmentId: number;
}

interface ICreateUserRequest {
    departments: string,
    filials: string,
    name: string,
    roles: string,
    secondName: string,
    surname: string,
    username: string
}

export const userAPI = createApi({
    reducerPath: 'userAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/users`}),
    tagTypes: ['User'],
    endpoints: (build) => ({
        getUsers: build.query<IUser[], void>({
            query: () => ({
                url: `/get-users`,
            }),
            providesTags: result => ['User']
        }),
        getUser: build.query<IUser, void>({
            query: () => ({
                url: `/get-user`,
            }),
            providesTags: result => ['User']
        }),
        createUser: build.mutation<any, ICreateUserRequest>({
            query: (post) => ({
                url: `/create-user`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['User']
        }),
        updateUser: build.mutation<any, IUpdateUserRequest>({
            query: (post) => ({
                url: `/update-user`,
                method: 'PATCH',
                body: post
            }),
            invalidatesTags: ['User']
        }),
    })
})
