import React, {useState} from "react";
import {
    DesktopOutlined,
    TeamOutlined,
    DatabaseOutlined,
    BarsOutlined,
    FileExcelOutlined,
    InfoCircleOutlined
} from "@ant-design/icons/";
import {MenuProps, Space, Spin, Typography} from "antd";
import {Breadcrumb, Layout, Menu, theme} from "antd";
import {userAPI} from "../services/UserService";
import {grey} from "@ant-design/colors";
import {useEffect} from "react";
import {useTimeout} from "../hooks/useTimeout";
import {TepModule} from "../modules/teps/TepModule";
import "../App.css";
import {useAppDispatch, useAppSelector} from "../hooks/redux";
import {setFilials, setDepartments, setRoles, setSettings, setStatuses, setUserInfo} from "../store/reducers/GeneralSlice";
import {DictModule} from "../modules/dicts/DictModule";
import {UserModule} from "../modules/administration/UserModule";
import {DepartmentModule} from "../modules/administration/DepartmentModule";
import {ConsolidatedByStructureReportModule} from "../modules/reports/ConsolidatedByStructureReportModule";
import {AboutFiilingReportModule} from "../modules/reports/AboutFiilingReportModule";
import {TEPDynamicReportModule} from "../modules/reports/TEPDynamicReportModule";
import {filialAPI} from "../services/FilialService";
import {PlanYearModule} from "../modules/administration/PlanYearModule";
import {InfoModule} from "../modules/info/InfoModule";
import {statusAPI} from "../services/StatusService";
import {roleAPI} from "../services/RoleService";
import {ConsolidatedByFilialReportModule} from "../modules/reports/ConsolidatedByFilialReportModule";
import {departmentAPI} from "../services/DepartmentService";

const {Title} = Typography;
const {Content, Sider} = Layout;

type MenuItem = Required<MenuProps>["items"][number];

const getItem = (
    label: React.ReactNode,
    key: React.Key,
    icon?: React.ReactNode,
    children?: MenuItem[]
): MenuItem => {
    return {
        key,
        icon,
        children,
        label,
    } as MenuItem;
}

export const MainScreen: React.FC = () => {
    const generalStore = useAppSelector(store => store.userReducer);
    const dispatch = useAppDispatch();
    const {data: user, error: errorUser, isLoading: isLoadingUser, refetch: refetchUser} = userAPI.useGetUserQuery();
    const {
        data: filials,
    } = filialAPI.useGetFilialsQuery();
    const {
        data: departments,
    } = departmentAPI.useGetDepartmentsQuery();
    const [getStatusesRequest, {
        data: statuses,
    }] = statusAPI.useGetStatusesMutation();
    const [getRolesRequest, {
        data: roles,
    }] = roleAPI.useGetRolesMutation();
    const [menuItems, setMenuItems] = useState<MenuItem[]>();
    const [selectedModule, setSelectedModule] = useState<string[]>([]);
    useEffect(() => {
        if (user) {
            dispatch(setSettings({year: user.year, startPeriod: user.startPeriod, endPeriod: user.endPeriod}))
            let availableModules: MenuItem[] = [];
            user.roles.forEach(role => {
                if (role.code === 0) {
                    availableModules.push(getItem("", "adm", <TeamOutlined/>, [
                        getItem("", "users"),
                        getItem("", "departments"),
                        getItem(" ", "planYear"),
                    ]));
                    setSelectedModule(['users'])
                }
                if (role.code === 1) {
                    availableModules.push(getItem("", "dicts", <DatabaseOutlined/>));
                    if (!availableModules.find((module: MenuItem) => {
                        if (module)
                            return module.key === 'teps';
                        else return false
                    }))
                        availableModules.push(getItem(" ", "teps", <BarsOutlined/>));
                    setSelectedModule(['dicts'])
                }
                if (role.code === 2) {
                    if (!availableModules.find((module: MenuItem) => {
                        if (module)
                            return module.key === 'teps';
                        else return false
                    }))
                        availableModules.push(getItem(" ", "teps", <BarsOutlined/>));

                    setSelectedModule(['teps'])
                }
                if (role.code === 3) {
                    if (!availableModules.find((module: MenuItem) => {
                        if (module) return module.key === 'teps';
                        else return false
                    })) {
                        availableModules.push(getItem(" ", "teps", <DesktopOutlined/>));
                    }
                    availableModules.push(getItem("", "reports", <FileExcelOutlined/>, [
                        getItem("  ", "reportOne"),
                        getItem("  ", "reportFour"),
                        getItem(" ", "reportTwo"),
                        getItem(" ", "reportThree"),
                    ]));
                    setSelectedModule(['teps'])
                }
                if (role.code === 4) {
                    if (!availableModules.find((module: MenuItem) => {
                        if (module)
                            return module.key === 'teps';
                        else return false
                    }))
                        availableModules.push(getItem(" ", "teps", <BarsOutlined/>));

                    setSelectedModule(['teps'])
                }
            });
            availableModules.push(getItem("", "info", <InfoCircleOutlined/>));
            setMenuItems(availableModules)
        }
    }, [user]);
    useEffect(() => {
        if (filials)
            dispatch(setFilials(filials));
    }, [filials])
    useEffect(() => {
        if (departments)
            dispatch(setDepartments(departments));
    }, [departments])
    useEffect(() => {
        getStatusesRequest();
        getRolesRequest();
    }, []);
    useEffect(() => {
        if (statuses) dispatch(setStatuses(statuses))
    }, [statuses])
    useEffect(() => {
        if (roles) dispatch(setRoles(roles))
    }, [roles])
    const [collapsed, setCollapsed] = useState(false);
    const {startRequestTimer, endRequestTimer, tick} = useTimeout();
    const {token: {colorBgContainer}} = theme.useToken();
    useEffect(() => {
        if (errorUser) {
            startRequestTimer();
        }
    }, [errorUser])
    useEffect(() => {
        if (tick > 10) {
            //refetchUser();
            endRequestTimer();
        }
    }, [tick])
    if (isLoadingUser)
        return (
            <div style={{
                width: '100vw',
                height: '100vh',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center'
            }}>
                <Spin size="large"/>
            </div>
        )
    else if (errorUser) {
        return (<Layout style={{minHeight: "100vh"}}>
            <Sider
                collapsible
                collapsed={collapsed}
                onCollapse={(value) => setCollapsed(value)}
                style={{position: 'fixed', overflow: 'auto', height: '100vh', left: 0, top: 0, bottom: 0}}
            >
                {!collapsed && <Title level={5} style={{
                    color: grey[0],
                    marginLeft: 15,
                    marginRight: 15,
                    marginTop: 15,
                    textAlign: 'center'
                }}>   .   .</Title>}
                <Menu
                    theme="dark"
                    defaultSelectedKeys={selectedModule}
                    mode="inline"
                    items={[{
                        key: 'info',
                        icon: <InfoCircleOutlined/>,
                        label: '',
                    } as MenuItem] as MenuItem[]}
                    onClick={({key, keyPath}) => {
                        setSelectedModule([key])
                    }}
                />
            </Sider>
            <Layout className={collapsed ? 'closeSideMenu site-layout' : 'openSideMenu site-layout'}>
                <Content style={{margin: "0 6px"}}>
                    <div
                        style={{
                            padding: 5,
                            height: '100vh',
                            background: colorBgContainer,
                        }}
                    >
                        <InfoModule/>
                    </div>
                </Content>
            </Layout>
        </Layout>)
    } else if (user && selectedModule.length > 0) {
        dispatch(setUserInfo(user));
        return (
            <Layout style={{minHeight: "100vh"}}>
                <Sider
                    collapsible
                    collapsed={collapsed}
                    onCollapse={(value) => setCollapsed(value)}
                    style={{position: 'fixed', overflow: 'auto', height: '100vh', left: 0, top: 0, bottom: 0}}
                >
                    {!collapsed && <Title level={5} style={{
                        color: grey[0],
                        marginLeft: 15,
                        marginTop: 15
                    }}>{user?.surname} {`${user?.name ? user?.name[0] : ''}. ${user?.secondName ? user?.secondName[0] : ''}.`}</Title>}
                    <Menu
                        theme="dark"
                        defaultSelectedKeys={selectedModule}
                        mode="inline"
                        items={menuItems}
                        onClick={({key, keyPath}) => {
                            setSelectedModule([key])
                        }}
                    />
                </Sider>
                <Layout className={collapsed ? 'closeSideMenu site-layout' : 'openSideMenu site-layout'}>
                    <Content style={{margin: "0 6px"}}>
                        <div
                            style={{
                                padding: 5,
                                height: '100vh',
                                background: colorBgContainer,
                            }}
                        >
                            {selectedModule[0] == 'teps' && <TepModule/>}
                            {selectedModule[0] == 'dicts' && <DictModule/>}
                            {selectedModule[0] == 'users' && <UserModule/>}
                            {selectedModule[0] == 'departments' && <DepartmentModule/>}
                            {selectedModule[0] == 'planYear' && <PlanYearModule/>}
                            {selectedModule[0] == 'reportOne' && <ConsolidatedByStructureReportModule/>}
                            {selectedModule[0] == 'reportTwo' && <AboutFiilingReportModule/>}
                            {selectedModule[0] == 'reportThree' && <TEPDynamicReportModule/>}
                            {selectedModule[0] == 'reportFour' && <ConsolidatedByFilialReportModule/>}
                            {selectedModule[0] == 'info' && <InfoModule/>}
                        </div>
                    </Content>
                </Layout>
            </Layout>
        );
    } else {
        return (
            <div style={{
                width: '100vw',
                height: '100vh',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center'
            }}>
                <Title level={5}>   </Title>
            </div>
        )
    }
};
