import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {host, port, secure} from "../config/config";

interface PreCreateNormfactorResponse {
    normfactorExist:boolean,
    normfactorExistInCurrentDict:boolean,
    number?:string,
    name?:string,
    dimension?:string,
    source?:string,
    department?:string
}

export const normfactorAPI = createApi({
    reducerPath: 'normfactorAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/normfactors`}),
    tagTypes: ['Normfactor'],
    endpoints: (build) => ({
        preCreateNormfactor: build.mutation<PreCreateNormfactorResponse, {number: string, dictId: number}>({
            query: (post) => ({
                url: `/pre-create-normfactor`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Normfactor']
        }),
        createNormfactors: build.mutation<any, { dictId: number, dimension: string, nfName: string, number: string, responsibilityStructure: string | undefined, source: string }>({
            query: (post) => ({
                url: `/create-normfactor`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Normfactor']
        }),
        updateNormfactors: build.mutation<any, { dictId: number, dimension: string, nfName: string, number: string, responsibilityStructure: string | undefined, source: string, nfId: number }>({
            query: (post) => ({
                url: `/update-normfactor`,
                method: 'PATCH',
                body: post
            }),
            invalidatesTags: ['Normfactor']
        }),
    })
})
