import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {host, port, secure} from "../config/config";
import {IDict} from "../models/IDict";
import {INormfactor} from "../models/INormfactor";

export interface Node {
    id: number,
    value: {title: string, items: {text: string}}
}

export interface Edge {
    source: number,
    target: number,
    value?: string,
}

export const dictAPI = createApi({
    reducerPath: 'dictAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/dicts`}),
    tagTypes: ['Dict'],
    endpoints: (build) => ({
        createDict: build.mutation<any, { dictName: string, filial: string, oooCode: string, paoCode: string, shortDictName: string }>({
            query: (post) => ({
                url: `/create-dict`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Dict']
        }),
        getDicts: build.mutation<IDict[], any>({
            query: (post) => ({
                url: `/get-dicts-list`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Dict']
        }),
        getDictsNormfactors: build.mutation<{ normfactors: INormfactor[], responsibilityStructure: { id: number, name: string }[] }, number>({
            query: (dictId) => ({
                url: `/get-dict-normfactors?dictId=${dictId}`,
                method: 'GET',
            }),
            invalidatesTags: ['Dict']
        }),
        getDictGraphData: build.mutation<{edges: Edge[], nodes: Node[]}, number>({
            query: (dictId) => ({
                url: `/get-dict-graph-data?dictId=${dictId}`,
                method: 'GET',
            }),
            invalidatesTags: ['Dict']
        }),
        getAllDicts: build.mutation<any, void>({
            query: (dictId) => ({
                url: `/get-all-dicts`,
                method: 'GET',
            }),
            invalidatesTags: ['Dict']
        }),
        updateDict: build.mutation<any, { dictName: string, filial: string, oooCode: string, paoCode: string, shortDictName: string, dictId: number }>({
            query: (post) => ({
                url: `/update-dict`,
                method: 'PATCH',
                body: post
            }),
            invalidatesTags: ['Dict']
        }),
    })
})