import {Alert, AutoComplete, Badge, Button, Input, Modal, Select, Spin, Typography} from "antd";
import React, {useEffect, useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {useAppSelector} from "../hooks/redux";
import {dictAPI, Edge, Node} from "../services/DictService";
import {IFilial} from "../models/IFilial";
import {DictGraph} from "./DictGraph"
import {departmentAPI} from "../services/DepartmentService";
import {IDict} from "../models/IDict";

const {Text} = Typography;

type ModalProps = {
    availableDicts: IDict[],
    data: { filials: string[], name: string, short_name: string, ooo_code: string, pao_code: string, id: number }|undefined,
    setData: Function,
    visible: boolean,
    setVisible: Function,
    emptyFieldsMessage: Function,
    getDicts: Function,
    successCreatedDictMessage: Function,
    errorCreatedDictMessage: Function,
    successUpdatedDictMessage: Function,
    errorUpdatedDictMessage: Function,
}

export const DictModal = (props: ModalProps) => {
    const generalStore = useAppSelector(store => store.userReducer);
    const [createDict, {
        data: createdDict,
        error: createdDictError,
        isLoading: createDictLoading
    }] = dictAPI.useCreateDictMutation();
    const [updateDict, {
        data: updatedDict,
        error: updateDictError,
        isLoading: updateDictLoading
    }] = dictAPI.useUpdateDictMutation();
    const [getDictGraphData, {
        data: dictGraphDataOriginal,
        error: dictGraphDataError,
        isLoading: dictGraphDataLoading
    }] = dictAPI.useGetDictGraphDataMutation();
    const [getAllDicts, {
        data: allDictsResponse,
        isLoading: getAllDictsLoading
    }] = dictAPI.useGetAllDictsMutation();
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(true);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const [showDictGraph, setShowDictGraph] = useState<boolean>(false);
    const [dictGraphData, setDictGraphData] = useState<{ edges: Edge[]; nodes: Node[]; }>();
    const [selectedFilials, setSelectedFilials] = useState<string[]>([]);
    const [fullDictName, setFullDictName] = useState<string>('');
    const [shortDictName, setShortDictName] = useState<string>('');
    const [paoCode, setPaoCode] = useState<string>('');
    const [oooCode, setOooCode] = useState<string>('');
    const [allDicts, setAllDicts] = useState<{value:string}[]>([]);
    const [existedDict, setExistedDict] = useState<boolean>(false);
    const [dictAlreadyAvailable, setDictAlreadyAvailable] = useState<boolean>(false);
    const handleSelectFilial = (value: string[]) => {
        setSelectedFilials(value);
    }
    const handleUpdateDict = () => {
        if (selectedFilials.length > 0 && fullDictName && shortDictName && paoCode && oooCode && props.data) {
            updateDict({
                dictId: props.data.id,
                dictName: fullDictName,
                filial: selectedFilials.map(filial => filial).join(';'),
                oooCode,
                paoCode,
                shortDictName
            })
        } else {
            props.emptyFieldsMessage()
        }
    }
    const handleCreateDict = () => {
        if (selectedFilials.length > 0 && ((fullDictName && shortDictName && paoCode && oooCode) || existedDict)) {
            let result = allDictsResponse.dicts.find((dict:any)=>dict.name===fullDictName);
            if (result)
                updateDict({
                    dictId: result.id,
                    dictName: fullDictName,
                    filial: selectedFilials.map(filial => filial).join(';'),
                    oooCode,
                    paoCode,
                    shortDictName
                })
            else
                createDict({
                dictName: fullDictName,
                filial: selectedFilials.map(filial => filial).join(';'),
                oooCode,
                paoCode,
                shortDictName
            })
        } else {
            props.emptyFieldsMessage()
        }
    }
    const handleOnSelectDictName = (fullDictName:string) => {
        let result = allDictsResponse.dicts.find((dict:any)=>dict.name===fullDictName);
        if (result) {
            if (props.data){
                if (props.data.id!==result.id){
                    setDictAlreadyAvailable(!(props.availableDicts.find((dict:IDict)=>dict.id===result.id)===undefined));
                    setExistedDict(true);
                }
            }
            else{
                setDictAlreadyAvailable(!(props.availableDicts.find((dict:IDict)=>dict.id===result.id)===undefined));
                setExistedDict(true);
            }
            setShortDictName(result.shortName);
            setPaoCode(result.paoCode);
            setOooCode(result.oooCode);
            setSelectedFilials(result.filials?.map((filial:{name:string, id:number})=>filial.name));
        }
    }
    const openDictGraphHandler = () => {
        if (props.data) {
            getDictGraphData(props.data.id);
        }
    }
    useEffect(()=>{
        if (dictGraphDataOriginal) {
            setDisabled(true);
            setShowDictGraph(true);
            setDictGraphData(dictGraphDataOriginal);
        }
    }, [dictGraphDataOriginal])
    useEffect(() => {
        if (!props.visible) {
            setSelectedFilials([]);
            setFullDictName('')
            setShortDictName('')
            setPaoCode('')
            setOooCode('')
            props.setData(null);
            setShowDictGraph(false);
            setExistedDict(false);
            setDictAlreadyAvailable(false);
        }
        else{
            if (props.data){
                setSelectedFilials(props.data.filials);
                setFullDictName(props.data.name);
                setShortDictName(props.data.short_name);
                setPaoCode(props.data.pao_code)
                setOooCode(props.data.ooo_code)
            }
            getAllDicts();
        }
    }, [props.visible])
    useEffect(()=>{
        if (allDictsResponse){
            setAllDicts(allDictsResponse.dicts.filter((dict:any)=>dict.name.length>0).map((dict:any)=>({value:dict.name})))
        }
    }, [allDictsResponse])
    useEffect(() => {
        if (updatedDict) {
            props.successUpdatedDictMessage();
            props.getDicts({});
            props.setVisible(false);
        }
    }, [updatedDict])
    useEffect(() => {
        if (updateDictError) {
            props.errorCreatedDictMessage();
        }
    }, [updateDictError])
    useEffect(() => {
        if (createdDict) {
            props.successCreatedDictMessage();
            props.getDicts({});
            props.setVisible(false);
        }
    }, [createdDict])
    useEffect(() => {
        if (createdDictError) {
            props.errorCreatedDictMessage();
        }
    }, [createdDictError])
    useEffect(()=>{
        if (fullDictName.trim().length===0){
            setSelectedFilials([]);
            setShortDictName('');
            setPaoCode('');
            setOooCode('');
        }
    }, [fullDictName])
    return (
        <Modal
            title={props.data ? ' ' : ' '}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={showDictGraph?"1250px":"550px"}
        >
            {(showDictGraph && dictGraphData)  ?
                <DictGraph data={dictGraphData} setVisible={setShowDictGraph}/>
                :
                <div style={{display: 'flex', flexDirection: 'column'}}>
                {(updateDictLoading || getAllDictsLoading) ?
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: 400}}>
                        <Spin size="large"/>
                    </div>
                    :
                    <>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            {/*<Input value={fullDictName} onChange={(e: any) => setFullDictName(e.target.value)}/>*/}
                            <AutoComplete
                                value={fullDictName}
                                allowClear={true}
                                onClear={() => {
                                    setExistedDict(false);
                                    setDictAlreadyAvailable(false);
                                }}
                                options={allDicts}
                                onChange={(e)=> {
                                    setFullDictName(e);
                                    let result = allDictsResponse.dicts.find((dict:any)=>dict.name===e);
                                    if (result) {
                                        if (props.data){
                                            if (props.data.id!==result.id){
                                                setExistedDict(true);
                                            }
                                        }
                                        else{
                                            setExistedDict(true);
                                        }
                                    }
                                    else setExistedDict(false);
                                }}
                                onSelect={(text)=>{
                                    setFullDictName(text);
                                    handleOnSelectDictName(text);
                                }}
                                filterOption={(inputValue, option)=>option!.value.toUpperCase().indexOf(inputValue.toUpperCase())!==-1}
                            />
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Input value={shortDictName} onChange={(e: any) => setShortDictName(e.target.value)} disabled={existedDict}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <Select
                                disabled={dictAlreadyAvailable || (props.data && existedDict)}
                                allowClear={true}
                                mode={'multiple'}
                                value={selectedFilials}
                                style={{width: 500}}
                                onChange={handleSelectFilial}
                                options={generalStore?.userInfo?.filials.map((filial: IFilial) => ({
                                    value: filial.name,
                                    label: filial.name
                                }))}
                                onDeselect={(filial:string)=>{
                                    if (allDictsResponse && existedDict) {
                                        let dict = allDictsResponse.dicts.find((dict: any) => dict.name === fullDictName);
                                        if (dict){
                                            if (dict.filials.find((f:{id:number, name:string})=>f.name===filial))
                                                setSelectedFilials(prev=>prev.concat(filial));
                                        }
                                    }
                                }}
                            />
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}>   </Text>
                            <Input disabled={existedDict} value={paoCode} onChange={(e: any) => setPaoCode(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}>     </Text>
                            <Input disabled={existedDict} value={oooCode} onChange={(e: any) => setOooCode(e.target.value)}/>
                        </div>
                        {(existedDict && !dictAlreadyAvailable && !props.data)  &&
                            <Alert style={{marginBottom: 10}} message="   ,       ,     " />
                        }
                        {(props.data && existedDict && !dictAlreadyAvailable) &&
                            <Alert style={{marginBottom: 10}} message="   ,    " />
                        }
                        {(props.data && existedDict && dictAlreadyAvailable) &&
                            <Alert style={{marginBottom: 10}} message="     " />
                        }
                        {(dictAlreadyAvailable && !props.data) &&
                            <Alert style={{marginBottom: 10}} message="    " />
                        }
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                             style={{display: 'flex', justifyContent: 'space-evenly', width: '100%', fontSize: 14}}>
                            {(props.data) ?
                                <>
                                    <Badge.Ribbon text={'Beta'} color={'green'} style={{marginTop: -15}}>
                                        <Button style={{width: '120px'}} onClick={openDictGraphHandler}></Button>
                                    </Badge.Ribbon>
                                    <Button disabled={(props.data && existedDict)} onClick={handleUpdateDict}></Button>
                                </>
                                :
                                <Button disabled={dictAlreadyAvailable} onClick={handleCreateDict}>{existedDict?'':''}</Button>
                            }
                            <Button onClick={() => props.setVisible(false)}></Button>
                        </div>
                    </>
                }
            </div>
            }
        </Modal>
    )
}