import {Button, Input, Modal, Select, Spin, Typography} from "antd";
import React, {useEffect, useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {userAPI} from "../services/UserService";
import {IUser} from "../models/IUser";
import {departmentAPI} from "../services/DepartmentService";
import {IStatus} from "../models/IStatus";
import {Simulate} from "react-dom/test-utils";
import change = Simulate.change;
import {useAppSelector} from "../hooks/redux";

const {Text} = Typography;
const {TextArea} = Input;

type ModalProps = {
    visible: boolean,
    setVisible: Function,
    handleUpdateTep: Function,
    declineExplanationZam: string,
    setStatus: Function,
    setDeclineExplanationZam: Function
}

export const ConfirmTepModal = (props: ModalProps) => {
    const generalStore = useAppSelector(store => store.userReducer);
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    return (
        <Modal
            title={'        ?'}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={"450px"}
        >
            <div style={{display: 'flex', flexDirection: 'column'}}>
                <div style={{display: 'flex', justifyContent: 'space-evenly'}}>
                    <Button style={{width: 100}} onClick={() => {
                        props.handleUpdateTep(generalStore?.statuses?.find((status: IStatus) => status.code === "3"), props.declineExplanationZam);
                        props.setVisible(false)
                        props.setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name)
                        props.setDeclineExplanationZam(props.declineExplanationZam);
                    }}></Button>
                    <Button style={{width: 100}} onClick={() => {
                        props.handleUpdateTep(generalStore?.statuses?.find((status: IStatus) => status.code === "3"), "");
                        props.setVisible(false)
                        props.setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name)
                        props.setDeclineExplanationZam("");
                    }}></Button>
                    <Button style={{width: 100}} onClick={() => props.setVisible(false)}></Button>
                </div>
            </div>
        </Modal>
    )
}