import {Divider, Modal, Radio, Switch, Typography} from "antd";
import {useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {GroupedColumnTepsGrid} from "../ENUMS";

const {Title, Text} = Typography;

type ModalProps = {
    moduleName: string,
    successUpdatedSettings: Function,
    visible: boolean,
    setVisible: Function,
    showBorders: boolean,
    setShowBorders: Function,
    showHeader: boolean,
    setShowHeader: Function,
    statusColored?: boolean,
    setStatusColored?: Function,
    previewText?: boolean,
    setPreviewText?: Function,
    selectedGroup?: GroupedColumnTepsGrid,
    setSelectedGroup?: Function,
    setGrouped?: Function,
    fixedNfNumberColumn?: boolean,
    fixedNfColumn?: boolean,
    fixedDimensionColumn?: boolean,
    setFixedNfColumn?: Function,
    setFixedNfNumberColumn?: Function,
    setFixedDimensionColumn?: Function,
}

export const GridSettingsModal = (props: ModalProps) => {
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const handleShowHeader = (checked: boolean) => {
        props.setShowHeader(checked);
        //localStorage.setItem(`tableHeader-${props.moduleName}`, String(checked));
        props.successUpdatedSettings();
    }
    const handleShowCellBorders = (checked: boolean) => {
        props.setShowBorders(checked);
        //localStorage.setItem(`tableBorders-${props.moduleName}`, String(checked));
        props.successUpdatedSettings();
    }
    const handleSetStatusColored = (checked: boolean) => {
        if (props.setStatusColored) {
            props.setStatusColored(checked)
            //localStorage.setItem(`tableColoredStatus-${props.moduleName}`, String(checked));
            props.successUpdatedSettings();
        }
    }
    const handleSetPreviewText = (checked: boolean) => {
        if (props.setPreviewText) {
            props.setPreviewText(checked)
            //localStorage.setItem(`tablePreviewText-${props.moduleName}`, String(checked));
            props.successUpdatedSettings();
        }
    }
    const handleSelectGroup = (e: any) => {
        if (props.setSelectedGroup && props.setGrouped) {
            props.setSelectedGroup(e.target.value)
            if (e.target.value === GroupedColumnTepsGrid.NONE)
                props.setGrouped({isGrouped: false, column: GroupedColumnTepsGrid.NONE})
            else if (e.target.value === GroupedColumnTepsGrid.DICT)
                props.setGrouped({isGrouped: true, column: GroupedColumnTepsGrid.DICT})
            else if (e.target.value === GroupedColumnTepsGrid.NF)
                props.setGrouped({isGrouped: true, column: GroupedColumnTepsGrid.NF})
            else if (e.target.value === GroupedColumnTepsGrid.RESP_STRUCT)
                props.setGrouped({isGrouped: true, column: GroupedColumnTepsGrid.RESP_STRUCT})
            else if (e.target.value === GroupedColumnTepsGrid.STATUS)
                props.setGrouped({isGrouped: true, column: GroupedColumnTepsGrid.STATUS})
            //localStorage.setItem(`tableSelectedGroup-${props.moduleName}`, e.target.value)
            props.successUpdatedSettings();
        }
    }
    return (
        <Modal
            title={''}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={"400px"}
        >
            <div style={{display: 'flex', flexDirection: 'column'}}>
                <Title style={{display: 'flex', alignSelf: 'center', marginBottom: 10}} level={5}></Title>
                <div style={{
                    width: 300,
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginBottom: 15
                }}>
                      
                    <Switch defaultChecked={props.showHeader} onChange={handleShowHeader}/>
                </div>
                <div style={{
                    width: 300,
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginBottom: 15,
                }}>
                      
                    <Switch defaultChecked={props.showBorders} onChange={handleShowCellBorders}/>
                </div>
                {/*{props.setStatusColored &&*/}
                {/*    <div style={{*/}
                {/*        width: 300,*/}
                {/*        display: 'flex',*/}
                {/*        justifyContent: 'space-between',*/}
                {/*        alignItems: 'center',*/}
                {/*        marginBottom: 15,*/}
                {/*    }}>*/}
                {/*         */}
                {/*        <Switch defaultChecked={props.statusColored} onChange={handleSetStatusColored}/>*/}
                {/*    </div>*/}
                {/*}*/}
                {/*{props.setPreviewText &&*/}
                {/*    <div style={{*/}
                {/*        width: 300,*/}
                {/*        display: 'flex',*/}
                {/*        justifyContent: 'space-between',*/}
                {/*        alignItems: 'center',*/}
                {/*    }}>*/}
                {/*           */}
                {/*        <Switch defaultChecked={props.previewText} onChange={handleSetPreviewText}/>*/}
                {/*    </div>*/}
                {/*}*/}
                {(props.setFixedDimensionColumn && props.setFixedNfColumn && props.setFixedNfNumberColumn) &&
                    <>
                        <Divider style={{width: '220px'}}/>
                        <div style={{
                            width: 400,
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'start',
                            flexDirection: 'column'
                        }}>
                            <Title style={{display: 'flex', alignSelf: 'center', marginBottom: 10}} level={5}>
                                </Title>
                            <div style={{
                                width: 300,
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center',
                                marginBottom: 15
                            }}>
                                 
                                <Switch defaultChecked={props.fixedNfNumberColumn}
                                        onChange={() => {
                                            if (props.setFixedNfNumberColumn) {
                                                props.setFixedNfNumberColumn((prev: boolean) => {
                                                    //if (props.fixedNfNumberColumn !== undefined)
                                                    //    localStorage.setItem('pinNfNumber-consolidatedReport', props.fixedNfNumberColumn.toString());
                                                    return !prev;
                                                });
                                            }
                                        }}/>
                            </div>
                            <div style={{
                                width: 300,
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center',
                                marginBottom: 15
                            }}>
                                  ()
                                <Switch defaultChecked={props.fixedNfColumn}
                                        onChange={() => {
                                            if (props.setFixedNfColumn && props.fixedNfColumn !== undefined) {
                                                props.setFixedNfColumn((prev: boolean) => {
                                                    //if (props.fixedNfColumn !== undefined)
                                                    //    localStorage.setItem('pinNfName-consolidatedReport', props.fixedNfColumn.toString());
                                                    return !prev;
                                                });
                                            }
                                        }}/>
                            </div>
                            <div style={{
                                width: 300,
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center',
                                marginBottom: 15
                            }}>
                                 
                                <Switch defaultChecked={props.fixedDimensionColumn}
                                        onChange={() => {
                                            if (props.setFixedDimensionColumn && props.fixedDimensionColumn !== undefined) {
                                                props.setFixedDimensionColumn((prev: boolean) => {
                                                    //if (props.fixedDimensionColumn !== undefined)
                                                    //    localStorage.setItem('pinDimension-consolidatedReport', props.fixedDimensionColumn.toString());
                                                    return !prev
                                                });
                                            }
                                        }}/>
                            </div>
                        </div>
                    </>
                }
                {(props.setSelectedGroup && props.setGrouped) &&
                    <>
                        <Divider style={{width: '220px'}}/>
                        <div style={{display: 'flex', flexDirection: 'column'}}>
                            <Title style={{display: 'flex', alignSelf: 'center', marginBottom: 10}} level={5}>
                                 </Title>
                            <Radio.Group style={{display: 'flex', flexDirection: 'column'}} onChange={handleSelectGroup}
                                         value={props.selectedGroup}>
                                <Radio value={GroupedColumnTepsGrid.NONE} style={{marginBottom: '5px'}}></Radio>
                                <Radio value={GroupedColumnTepsGrid.DICT} style={{marginBottom: '5px'}}></Radio>
                                <Radio value={GroupedColumnTepsGrid.NF} style={{marginBottom: '5px'}}>
                                    </Radio>
                                <Radio value={GroupedColumnTepsGrid.RESP_STRUCT} style={{marginBottom: '5px'}}>
                                    </Radio>
                                <Radio value={GroupedColumnTepsGrid.STATUS} style={{marginBottom: '5px'}}></Radio>
                            </Radio.Group>
                        </div>
                    </>
                }
            </div>
        </Modal>
    )
}