import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {host, port, secure} from "../config/config";

export interface IReport1 {
    nfNumber: number;
    nf: string;
    dict: string;
    dimension: string;
    filials: { name: string, value: string }[]
}

export interface IReport4 {
    nfNumber: number;
    nf: string;
    dict: string;
    dimension: string;
    departments: { name: string, value: string }[]
}

export interface IReport2 {
    filial: string;
    filled: string;
    confirmed: string;
    onConfirmed: string;
}

export interface IReport3 {
    nfNumber: string;
    nf: string;
    dictL: string;
    dimension: string;
    yearsValue: { year: string, value: string }[]
}

export const reportAPI = createApi({
    reducerPath: 'reportAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/teps`}),
    tagTypes: ['Report'],
    endpoints: (build) => ({
        getReport1: build.mutation<IReport1[], { department: string, year: string }>({
            query: (post) => ({
                url: `/get-consolidated-report-teps`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Report']
        }),
        getReport2: build.mutation<IReport2[], string>({
            query: (year) => ({
                url: `/get-pivot-grid-data?year=${year}`,
                method: 'GET',
            }),
            invalidatesTags: ['Report']
        }),
        getReport3: build.mutation<IReport3[], { filial: string }>({
            query: (post) => ({
                url: `/get-dynamic-report`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Report']
        }),
        getReport4: build.mutation<IReport4[], { filial: string, year: string }>({
            query: (post) => ({
                url: `/get-consolidated-report-by-filial`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Report']
        }),
    })
})
