import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {host, port, secure} from "../config/config";
import {ITep} from "../models/ITep";

export interface GetPrevYearValueRequest {
    normfactor: string;
    filial: string;
    year: string;
}

export interface GetPrevYearValueResponse {
    value: string;
}

export interface CreateTepRecordsRequest {
    filialId: string,
    year: string,
}

export interface GetTepRecordsRequest {
    filialId: string,
    planYear: string,
    roleCode: number
}

export interface GetNumOfNewTepsRequest {
    filialId: string,
    year: string
}

export const tepsAPI = createApi({
    reducerPath: 'tepAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/teps`}),
    tagTypes: ['Tep'],
    endpoints: (build) => ({
        getTeps: build.mutation<ITep[], GetTepRecordsRequest>({
            query: (post) => ({
                url: `/get-tep-records`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Tep']
        }),
        updateTep: build.mutation<any, any>({
            query: (post) => ({
                url: `/update-tep-record`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Tep']
        }),
        getNumOfNewTeps: build.mutation<any, GetNumOfNewTepsRequest>({
            query: (post) => ({
                url: `/pre-create-tep-record`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Tep']
        }),
        createNewTeps: build.mutation<any, CreateTepRecordsRequest>({
            query: (post) => ({
                url: `/create-tep-record`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Tep']
        }),
        updateTepsStatuses: build.mutation<any, any>({
            query: (post) => ({
                url: `/update-teps-statuses`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Tep']
        }),
        getPrevYearValue: build.mutation<GetPrevYearValueResponse, GetPrevYearValueRequest>({
            query: (post) => ({
                url: `/get-prev-year-value`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Tep']
        })
    })
})
