import React, {useEffect, useState} from 'react';
import {Button, DatePicker, message, Table, Tag, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {SettingOutlined} from "@ant-design/icons";
import {CellSize} from "../../ENUMS";
// @ts-ignore
import Highlighter from "react-highlight-words"
import "../../App.css";
import {userAPI} from "../../services/UserService";
import {settingsAPI} from "../../services/SettingsService";
import dayjs from 'dayjs'
import {useAppDispatch, useAppSelector} from "../../hooks/redux";
import {setSettings, setUserInfo} from "../../store/reducers/GeneralSlice";
import {start} from "repl";


const {RangePicker} = DatePicker;

const {Text} = Typography;

export const PlanYearModule: React.FC = () => {
    // Create Toasts context
    const [messageApi, contextHolder] = message.useMessage();
    //End of toasts
    // Requests
    const [updateSettingsRequest, {
        data: response,
        error: updateUserError,
        isSuccess: isSuccess,
        isLoading: updateUserLoading
    }] = settingsAPI.useUpdateSettingsMutation();
    // End of requests
    const dispatch = useAppDispatch();
    const userStore = useAppSelector(store => store.userReducer);
    const [year, setYear] = useState<string>(userStore.settings.year);
    const [startPeriod, setStartPeriod] = useState<string>(userStore.settings.startPeriod);
    const [endPeriod, setEndPeriod] = useState<string>(userStore.settings.endPeriod);
    const updateSettings = (year: string, startPeriod: string, endPeriod: string) => {
        updateSettingsRequest({
            "year": year,
            "startPeriod": startPeriod,
            "endPeriod": endPeriod
        });
    }
    useEffect(() => {
        if (isSuccess)
            dispatch(setSettings({
                year,
                startPeriod,
                endPeriod
            }))
    }, [isSuccess])
    console.log(startPeriod)
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            <div style={{display: 'flex', alignItems: 'center', marginBottom: 15, marginLeft: 15}}>
                <Text style={{width: 220, marginRight: 10, fontSize: '14px', lineHeight: '22px'}}> </Text>
                <DatePicker allowClear={false} value={dayjs(`01-01-${year}`, 'DD-MM-YYYY')} picker={"year"}
                            onChange={(e: any, year: string) => {
                                setYear(year);
                                updateSettings(year, startPeriod, endPeriod);
                            }}/>
            </div>
            <div style={{display: 'flex', alignItems: 'center', marginBottom: 15, marginLeft: 15}}>
                <Text style={{width: 220, marginRight: 10, fontSize: '14px', lineHeight: '22px'}}> 
                    </Text>
                <RangePicker
                    format={'DD-MM-YYYY'}
                    allowClear={false} value={[dayjs(`${startPeriod}`, 'DD-MM-YYYY'), dayjs(`${endPeriod}`, 'DD-MM-YYYY'),]}
                             onChange={(e: any, period: string[]) => {
                                 setStartPeriod(period[0]);
                                 setEndPeriod(period[1]);
                                 updateSettings(year, period[0], period[1]);
                             }}/>
            </div>
        </div>
    );
}