import {Button, Input, Modal, Select, Spin, Typography} from "antd";
import React, {useEffect, useRef, useState} from "react";
import Dragable, {DraggableData, DraggableEvent} from "react-draggable";
import {userAPI} from "../services/UserService";
import {IUser} from "../models/IUser";
import {departmentAPI} from "../services/DepartmentService";

const {Text} = Typography;
const {TextArea} = Input;

type ModalProps = {
    visible: boolean,
    setVisible: Function,
    emptyFieldsMessage: Function,
    getDepartments: Function,
    successCreatedDepartmentMessage: Function,
    errorCreatedDepartmentMessage: Function,
    successUpdatedDepartmentMessage: Function,
    errorUpdatedDepartmentMessage: Function,
    data: {
        id: number,
        name: string,
        users: string[]
    }|undefined,
    setData: Function,
}

export const DepartmentModal = (props: ModalProps) => {
    const [createDepartment, {
        data: createdDepartment,
        error: createDepartmentError,
        isLoading: createDepartmentLoading
    }] = departmentAPI.useCreateDepartmentMutation();
    const {
        data: users,
        error: getUsersError,
        isLoading: getUsersIsLoading,
        refetch: refetchUsers
    } = userAPI.useGetUsersQuery();
    const [updateDepartment, {
        data: updatedDepartment,
        error: updateDepartmentError,
        isLoading: updateDepartmentLoading
    }] = departmentAPI.useUpdateDepartmentMutation();
    // Draggable settings
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    // End of dnd
    const [name, setName] = useState<string>();
    const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
    const handleSelectUsers = (value: string[]) => {
        setSelectedUsers(value);
    }
    const handleCreateDepartment = () => {
        if (name) {
            createDepartment({name, users: selectedUsers.join(';')});
        } else {
            props.emptyFieldsMessage()
        }
    }
    const handleUpdateDepartment = () => {
        if (name && props.data) {
            updateDepartment({id: props.data.id, name, users: selectedUsers.join(';')} as any);
        } else {
            props.emptyFieldsMessage()
        }
    }
    useEffect(() => {
        if (!props.visible) {
            setName('');
            setSelectedUsers([]);
            props.setData(null);
        }
        else if (props.data){
            setName(props.data.name);
            setSelectedUsers(props.data.users);
        }
    }, [props.visible])
    useEffect(() => {
        if (createdDepartment) {
            props.successCreatedDepartmentMessage();
            props.getDepartments();
            props.setVisible(false);
        }
    }, [createdDepartment])
    useEffect(() => {
        if (createDepartmentError) {
            props.errorCreatedDepartmentMessage();
        }
    }, [createDepartmentError])
    useEffect(() => {
        if (updatedDepartment) {
            props.successUpdatedDepartmentMessage();
            props.getDepartments();
            props.setVisible(false);
        }
    }, [updatedDepartment])
    useEffect(() => {
        if (updateDepartmentError) {
            props.errorCreatedDepartmentMessage();
        }
    }, [updateDepartmentError])
    return (
        <Modal
            title={props.data ? ' ' : ' '}
            centered={true}
            open={props.visible}
            onCancel={() => props.setVisible(false)}
            footer={[]}
            modalRender={(modal) => (
                // @ts-ignore
                <Dragable
                    disabled={disabled}
                    bounds={bounds}
                    onStart={(event, uiData) => onStart(event, uiData)}
                >
                    <div ref={draggableRef}>{modal}</div>
                </Dragable>
            )}
            width={"450px"}
        >
            <div style={{display: 'flex', flexDirection: 'column'}}>
                {createDepartmentLoading ?
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: 400}}>
                        <Spin size="large"/>
                    </div>
                    :
                    <>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}></Text>
                            <TextArea value={name} onChange={(e: any) => setName(e.target.value)}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            marginTop: 5,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Select
                                mode={'multiple'}
                                value={selectedUsers}
                                style={{width: 400}}
                                onChange={handleSelectUsers}
                                options={users && users.filter((user: IUser) => user.roles.find(role => role.code === 3)).map((user: IUser) => ({
                                    value: user.username,
                                    label: user.username
                                }))}
                            />
                        </div>

                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                             style={{display: 'flex', justifyContent: 'space-evenly', width: '100%', fontSize: 14}}>
                            {props.data ?
                                <Button onClick={handleUpdateDepartment}></Button>
                                :
                                <Button onClick={handleCreateDepartment}></Button>
                            }
                            <Button onClick={() => props.setVisible(false)}></Button>
                        </div>
                    </>
                }
            </div>
        </Modal>
    )
}