import React, {useEffect, useState} from 'react';
import {Button, message, Select, Table, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {FileExcelOutlined, SettingOutlined} from "@ant-design/icons";
// @ts-ignore
import Highlighter from "react-highlight-words"
import "../../App.css";
import {useTimeout} from "../../hooks/useTimeout";
import {IReport2, reportAPI} from "../../services/ReportService";
import {host, port, secure} from "../../config/config";
import {useAppSelector} from "../../hooks/redux";
import {GridSettingsModal} from "../../components/GridSettingsModal";

const {Text, Title} = Typography;

interface ListItem {
    text: string;
    value: string;
}

interface DataType extends IReport2 {
    key: React.Key;
}

export const AboutFiilingReportModule: React.FC = () => {
    const generalStore = useAppSelector(store => store.userReducer);
    // Create Toasts context
    const [messageApi, contextHolder] = message.useMessage();
    const successUpdatedSettings = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successLoadReport = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const warningLoadReport = () => {
        messageApi.open({
            type: 'warning',
            content: '  ',
            duration: 4,
        });
    }
    const errorLoadReports = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    //End of toasts
    // Requests
    const [getRecords, {
        data: records,
        error: getRecordsError,
        isLoading: getRecordsLoading
    }] = reportAPI.useGetReport2Mutation();
    // End of requests
    const [data, setData] = useState<DataType[]>();
    const [visibleSettings, setVisibleSettings] = useState<boolean>(false);
    const [showHeader, setShowHeader] = useState<boolean>(localStorage.getItem('tableHeader-aboutFillingReport') ? localStorage.getItem('tableHeader-aboutFillingReport') === 'true' : true);
    const [showCellBorders, setShowBorders] = useState<boolean>(localStorage.getItem('tableBorders-aboutFillingReport') ? localStorage.getItem('tableBorders-aboutFillingReport') === 'true' : true);
    const [selectedYear, setSelectedYear] = useState<string>(generalStore.settings.year);
    const [years, setYears] = useState<{ value: string, label: string }[]>([]);
    useEffect(() => {
        if (records) {
            successLoadReport();
            setData(records.map((record: IReport2, key: number) => ({...record, key} as DataType)));
        }
    }, [records])
    useEffect(() => {
        if (getRecordsError) {
            errorLoadReports();
        }
    }, [getRecordsError]);
    useEffect(() => {
        let years: { label: string, value: string }[] = [];
        for (let year = 2018; year <= Number(generalStore.settings.year); year++) {
            years.push({label: String(year), value: String(year)})
        }
        setYears(years);
    }, []);
    const columns: ColumnsType<DataType> = [
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: '11px', maxHeight: 100, overflow: 'hidden'}}></Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: 12,
                        color: 'black',
                        height: '30px',
                        overflowY: 'scroll'
                    } : {fontSize: 12, color: 'black', height: '30px'}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'filial',
            key: 'filial',
            width: '55%',
        },
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: '11px', maxHeight: 100, overflow: 'hidden'}}> </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: 12,
                        color: 'black',
                        height: '30px',
                        overflowY: 'scroll'
                    } : {fontSize: 12, color: 'black', height: '30px'}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'filled',
            key: 'filled',
            width: '15%',
        },
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: '11px', maxHeight: 100, overflow: 'hidden'}}> </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: 12,
                        color: 'black',
                        height: '30px',
                        overflowY: 'scroll'
                    } : {fontSize: 12, color: 'black', height: '30px'}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'onConfirmed',
            key: 'onConfirmed',
            width: '15%',
        },
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: '11px', maxHeight: 100, overflow: 'hidden'}}></Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: 12,
                        color: 'black',
                        height: '30px',
                        overflowY: 'scroll'
                    } : {fontSize: 12, color: 'black', height: '30px'}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'confirmed',
            key: 'confirmed',
            width: '15%',
        },
    ];
    const handleUpdateYear = (value: string) => {
        setSelectedYear(value);
    }
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            <GridSettingsModal
                moduleName={'aboutFillingReport'}
                successUpdatedSettings={successUpdatedSettings}
                visible={visibleSettings}
                setVisible={setVisibleSettings}
                showBorders={showCellBorders}
                showHeader={showHeader}
                setShowHeader={setShowHeader}
                setShowBorders={setShowBorders}
            />
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginLeft: '15px',
                marginRight: '15px',
                marginBottom: '10px'
            }}>
                <div style={{display: 'flex'}}>
                    <Select
                        disabled={getRecordsLoading}
                        defaultValue={selectedYear}
                        style={{width: '190px'}}
                        onChange={handleUpdateYear}
                        options={years}
                    />
                    <Button disabled={getRecordsLoading} style={{marginLeft: 15}} onClick={() => {
                        getRecords(selectedYear);
                    }}></Button>
                </div>
                <div>
                    <Tooltip placement={'leftTop'} title={' '}>
                        <Button style={{marginRight: 10}} className='settingsButton' icon={<FileExcelOutlined/>}
                                shape={'circle'} onClick={() => {
                            warningLoadReport();
                            let tmpButton = document.createElement('a')
                            tmpButton.href = `${secure}://${host}:${port}/api/report/export2?year=${selectedYear}`
                            tmpButton.click()
                        }}/>
                    </Tooltip>
                    <Tooltip placement={'leftTop'} title={' '}>
                        <Button className='settingsButton' icon={<SettingOutlined/>} shape={'circle'}
                                onClick={() => setVisibleSettings(true)}/>
                    </Tooltip>
                </div>
            </div>
            <Table
                bordered={showCellBorders}
                loading={getRecordsLoading}
                showHeader={showHeader}
                columns={columns}
                dataSource={data}
                pagination={{position: ["bottomRight"], defaultPageSize: 30}}
            />
        </div>
    );
}