import {IUser} from "../../models/IUser";
import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {IFilial} from "../../models/IFilial";
import {IStatus} from "../../models/IStatus";
import {IRole} from "../../models/IRole";
import {IDepartment} from "../../models/IDepartment";

interface generalState {
    userInfo: IUser|null;
    filials: IFilial[];
    departments: IDepartment[];
    statuses: IStatus[];
    roles: IRole[];
    maxYear: number,
    settings: {
        year: string,
        startPeriod: string,
        endPeriod: string
    }
}

const initialState: generalState = {
    userInfo: null,
    filials: [],
    statuses: [],
    departments: [],
    roles: [],
    maxYear: new Date().getFullYear(),
    settings: {
        year: '',
        startPeriod: '',
        endPeriod: ''
    }
}

export const generalSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        setUserInfo(state, action: PayloadAction<IUser>) {
            state.userInfo = action.payload
        },
        setFilials(state, action: PayloadAction<IFilial[]>) {
            state.filials = action.payload
        },
        setDepartments(state, action: PayloadAction<IDepartment[]>) {
            state.departments = action.payload
        },
        setSettings(state, action: PayloadAction<any>) {
            state.settings = action.payload
        },
        setStatuses(state, action: PayloadAction<IStatus[]>) {
            state.statuses = action.payload
        },
        setRoles(state, action: PayloadAction<IRole[]>) {
            state.roles = action.payload
        }
    }
})

export const {setUserInfo, setDepartments, setFilials, setSettings, setStatuses, setRoles} = generalSlice.actions;
export default generalSlice.reducer;
