import React, {useEffect, useRef, useState} from 'react';
import {Button, message, Select, Table, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {FileExcelOutlined, SettingOutlined} from "@ant-design/icons";
// @ts-ignore
import Highlighter from "react-highlight-words"
import "../../App.css";
import {IReport3, reportAPI} from "../../services/ReportService";
import {host, port, secure} from "../../config/config";
import {IFilial} from "../../models/IFilial";
import {useAppSelector} from "../../hooks/redux";
import {GridSettingsModal} from "../../components/GridSettingsModal";

const {Text} = Typography;

interface DataType extends IReport3 {
    key: React.Key;
}

export const TEPDynamicReportModule: React.FC = () => {
    const generalStore = useAppSelector(store => store.userReducer);
    // Create Toasts context
    const [messageApi, contextHolder] = message.useMessage();
    const successUpdatedSettings = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successLoadReport = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const warningLoadReport = () => {
        messageApi.open({
            type: 'warning',
            content: '  ',
            duration: 4,
        });
    }
    const errorLoadReport = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    //End of toasts
    // Requests
    const [getRecords, {
        data: records,
        error: getRecordsError,
        isLoading: getRecordsLoading
    }] = reportAPI.useGetReport3Mutation();
    // End of requests
    const [data, setData] = useState<DataType[]>();
    const [visibleSettings, setVisibleSettings] = useState<boolean>(false);
    const [showHeader, setShowHeader] = useState<boolean>(localStorage.getItem('tableHeader-tepDynamicReport') ? localStorage.getItem('tableHeader-tepDynamicReport') === 'true' : true);
    const [showCellBorders, setShowBorders] = useState<boolean>(localStorage.getItem('tableBorders-tepDynamicReport') ? localStorage.getItem('tableBorders-tepDynamicReport') === 'true' : true);
    const [selectedFilial, setSelectedFilial] = useState<string>(generalStore?.userInfo?.filials[0]?.name ?? '');
    const [columns, setColumns] = useState<ColumnsType<DataType>>([]);
    const [rowHeight, setRowHeight] = useState<number>(30);
    const [counter, setCounter] = useState(0);
    const numberColumnHeader = useRef(null);
    useEffect(()=>{
        if (numberColumnHeader && data && counter<1)
            if (numberColumnHeader.current) {
                setCounter(prev=>prev+=1)
                //@ts-ignore
                numberColumnHeader.current.click()
            }
    }, [columns, data])
    useEffect(() => {
        if (records) {
            successLoadReport();
            setData(records.map((record: IReport3, key: number) => {
                let result = {...record, key};
                record.yearsValue.map((yearValue: { value: string, year: string }) => {
                    // @ts-ignore
                    result[yearValue.year] = yearValue.value
                })
                return result;
            }));
            setColumns([
                {
                    title: () => {
                        return <Text className="dragHandler" ref={numberColumnHeader}
                                     style={{
                                         color: blue.primary,
                                         fontSize: '11px',
                                         height: rowHeight,
                                         overflow: 'hidden'
                                     }}> </Text>
                    },
                    render(text, record) {
                        return {
                            props: {
                                style: {color: 'red'}
                            },
                            children: <div style={text?.length > 70 ? {
                                fontSize: 12,
                                color: 'black',
                                height: '57px',
                                overflowY: 'scroll'
                            } : {fontSize: 12, color: 'black', height: '57px'}}>
                                <div>{text}</div>
                            </div>
                        };
                    },
                    dataIndex: 'nfNumber',
                    key: 'nfNumber',
                    width: 110,
                    sorter: (a: any, b: any) => a.nfNumber - b.nfNumber,
                    defaultSortOrder: 'ascend',
                    sortDirections: ['descend', 'ascend'],
                },
                {
                    title: () => {
                        return <Text className="dragHandler"
                                     style={{
                                         color: blue.primary,
                                         fontSize: '11px',
                                         height: rowHeight,
                                         overflow: 'hidden'
                                     }}>
                            </Text>
                    },
                    render(text, record) {
                        return {
                            props: {
                                style: {color: 'red'}
                            },
                            children: <div style={text?.length > 70 ? {
                                fontSize: 12,
                                color: 'black',
                                height: '57px',
                                overflowY: 'scroll'
                            } : {fontSize: 12, color: 'black', height: '57px'}}>
                                <div>{text}</div>
                            </div>
                        };
                    },
                    dataIndex: 'nf',
                    key: 'nf',
                    width: 300,
                },
                {
                    title: () => {
                        return <Text className="dragHandler"
                                     style={{
                                         color: blue.primary,
                                         fontSize: '11px',
                                         height: rowHeight,
                                         overflow: 'hidden'
                                     }}></Text>
                    },
                    render(text, record) {
                        return {
                            props: {
                                style: {color: 'red'}
                            },
                            children: <div style={text?.length > 70 ? {
                                fontSize: 12,
                                color: 'black',
                                height: '57px',
                                overflowY: 'scroll'
                            } : {fontSize: 12, color: 'black', height: '57px'}}>
                                <div>{text}</div>
                            </div>
                        };
                    },
                    dataIndex: 'dict',
                    key: 'dict',
                    width: 300,
                },
                {
                    title: () => {
                        return <Text className="dragHandler"
                                     style={{
                                         color: blue.primary,
                                         fontSize: '11px',
                                         height: rowHeight,
                                         overflow: 'hidden'
                                     }}>. .</Text>
                    },
                    render(text, record) {
                        return {
                            props: {
                                style: {color: 'red'}
                            },
                            children: <div style={text?.length > 70 ? {
                                fontSize: 12,
                                color: 'black',
                                height: '57px',
                                overflowY: 'scroll'
                            } : {fontSize: 12, color: 'black', height: '57px'}}>
                                <div>{text}</div>
                            </div>
                        };
                    },
                    dataIndex: 'dimension',
                    key: 'dimension',
                    width: 100,
                },
                {
                    title: () => {
                        return <Text className="dragHandler"
                                     style={{
                                         color: blue.primary,
                                         fontSize: '11px',
                                         height: rowHeight,
                                         overflow: 'hidden'
                                     }}>  ()</Text>
                    },
                    children: records[0]?.yearsValue?.map((yearValue: { year: string, value: string }) => ({
                        title: () => {
                            return <Text style={{
                                margin: 0,
                                color: blue.primary,
                                fontSize: '11px',
                                width: 40,
                                height: rowHeight,
                                overflow: 'hidden'
                            }}>{yearValue.year}</Text>
                        },
                        render(text: any, record: any) {
                            return {
                                props: {
                                    style: {color: 'red'}
                                },
                                children: <div style={{fontSize: 12, color: 'black', height: rowHeight}}>
                                    <div>{text !== "null" ? text: ""}</div>
                                </div>
                            };
                        },
                        dataIndex: yearValue.year,
                        key: yearValue.year,
                    })),
                    width: 100,

                },
            ])
        }
    }, [records])
    useEffect(() => {
        if (getRecordsError) {
            errorLoadReport();
        }
    }, [getRecordsError]);
    useEffect(() => {
        //getRecords({filial: selectedFilial});
    }, []);
    const handleUpdateFilial = (filial: string) => {
        setSelectedFilial(filial);
    }
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            <GridSettingsModal
                moduleName={'tepDynamicReport'}
                successUpdatedSettings={successUpdatedSettings}
                visible={visibleSettings}
                setVisible={setVisibleSettings}
                showBorders={showCellBorders}
                showHeader={showHeader}
                setShowHeader={setShowHeader}
                setShowBorders={setShowBorders}
            />
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginLeft: '15px',
                marginRight: '15px',
                marginBottom: '10px'
            }}>
                <div style={{display: 'flex'}}>
                    <Select
                        disabled={getRecordsLoading}
                        defaultValue={selectedFilial}
                        style={{width: '290px', marginRight: '15px'}}
                        onChange={handleUpdateFilial}
                        options={generalStore?.userInfo?.filials.map((filial: IFilial) => ({
                            value: filial.name,
                            label: filial.name
                        }))}
                    />
                    <Button disabled={getRecordsLoading} style={{marginRight: 10}} onClick={() => {
                        getRecords({filial: selectedFilial});
                    }}></Button>
                </div>
                <div>
                    <Tooltip placement={'leftTop'} title={' '}>
                        <Button style={{marginRight: 10}} className='settingsButton' icon={<FileExcelOutlined/>}
                                shape={'circle'} onClick={() => {
                            warningLoadReport();
                            let tmpButton = document.createElement('a')
                            tmpButton.href = `${secure}://${host}:${port}/api/report/dynamicReport?filial=${selectedFilial}`
                            tmpButton.click()
                        }}/>
                    </Tooltip>
                    <Tooltip placement={'leftTop'} title={' '}>
                        <Button className='settingsButton' icon={<SettingOutlined/>} shape={'circle'}
                                onClick={() => setVisibleSettings(true)}/>
                    </Tooltip>
                </div>
            </div>
            <Table
                bordered={showCellBorders}
                loading={getRecordsLoading}
                showHeader={showHeader}
                columns={columns}
                dataSource={data}
                pagination={{position: ["bottomRight"], defaultPageSize: 11}}
            />
        </div>
    );
}