import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {IUser} from "../models/IUser";
import {host, port, secure} from "../config/config";
import {IDepartment} from "../models/IDepartment";

interface ICreateDepartmentRequest {
    name: string;
    users: string;
}

interface IUpdateDepartmentRequest {
    id: any;
    name: string;
    users: string;
}

export const departmentAPI = createApi({
    reducerPath: 'departmentAPI',
    baseQuery: fetchBaseQuery({baseUrl: `${secure}://${host}:${port}/api/departments`}),
    tagTypes: ['Department'],
    endpoints: (build) => ({
        getDepartments: build.query<IDepartment[], void>({
            query: () => ({
                url: `/get-departments`,
            }),
            providesTags: result => ['Department']
        }),
        createDepartment: build.mutation<any, ICreateDepartmentRequest>({
            query: (post) => ({
                url: `/create-department`,
                method: 'POST',
                body: post
            }),
            invalidatesTags: ['Department']
        }),
        updateDepartment: build.mutation<any, IUpdateDepartmentRequest>({
            query: (post) => ({
                url: `/update-department`,
                method: 'PATCH',
                body: post
            }),
            invalidatesTags: ['Department']
        }),
    })
})
