import React, {useEffect, useState} from 'react';
import {Button, message, Table, Tag, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {SettingOutlined} from "@ant-design/icons";
import {CellSize} from "../../ENUMS";
// @ts-ignore
import Highlighter from "react-highlight-words"
import {SizeType} from "antd/es/config-provider/SizeContext";
import "../../App.css";
import {departmentAPI} from "../../services/DepartmentService";
import {IDepartment} from "../../models/IDepartment";
import {userAPI} from "../../services/UserService";
import {GridSettingsModal} from "../../components/GridSettingsModal";
import {DepartmentModal} from "../../components/DepartmentModal";

const {Text} = Typography;

interface DataType extends IDepartment {
    key: React.Key;
}

export const DepartmentModule: React.FC = () => {
    // Messages
    const [messageApi, contextHolder] = message.useMessage();
    const successUpdatedSettingsMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successLoadDepartmentsMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorLoadDepartmentsMessage = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const successCreatedDepartmentMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successUpdatedDepartmentMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorCreatedDepartmentMessage = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const errorUpdatedDepartmentMessage = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const emptyFieldsMessage = () => {
        messageApi.open({
            type: 'warning',
            content: '   ',
            duration: 1,
        });
    }
    //End of messages
    // Requests
    const {
        data: users,
        error: getUsersError,
        isLoading: getUsersIsLoading,
        refetch: refetchUsers
    } = userAPI.useGetUsersQuery();
    const {
        data: departments,
        error: getDepartmentsError,
        isLoading: getDepartmentsLoading,
        refetch: refetchDepartments
    } = departmentAPI.useGetDepartmentsQuery();
    // End of requests
    const [data, setData] = useState<DataType[]>();
    const [visibleSettings, setVisibleSettings] = useState<boolean>(false);
    const [visible, setVisible] = useState<boolean>(false);
    const [selectedRecordData, setSelectedRecordData] = useState({id: 0, name: '', users: []});
    const [showHeader, setShowHeader] = useState<boolean>(localStorage.getItem('showHeaderDepartmentsGrid') ? localStorage.getItem('showHeaderDepartmentsGrid') === 'true' : true);
    const [showCellBorders, setShowBorders] = useState<boolean>(localStorage.getItem('showBordersDepartmentsGrid') === 'true');
    const [titleFontSize, ] = useState('13px');
    const [tableCellFontSize, ] = useState('15px');
    useEffect(() => {
        if (departments) {
            setData(departments.map((dep: IDepartment, key: number) => ({...dep, key} as DataType)));
            successLoadDepartmentsMessage();
        }
    }, [departments]);
    useEffect(() => {
        if (getUsersError) {
            errorLoadDepartmentsMessage();
        }
    }, [getUsersError]);
    useEffect(() => {
        refetchUsers();
        refetchDepartments();
    }, []);
    const columns: ColumnsType<DataType> = [
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                     </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: '50px',
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: '30px'}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'name',
            key: 'name',
            width: '50%',
        },
        {
            title: () => {
                return <Text style={{
                    color: blue.primary,
                    fontSize: titleFontSize,
                    maxHeight: 100,
                    overflow: 'hidden'
                }}></Text>
            },
            render(users, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={{fontSize: tableCellFontSize, color: 'black', height: '30px'}}>
                        {users.map((user: string) => <Tag color='geekblue' style={{marginBottom: 3}}>{user}</Tag>)}
                    </div>
                };
            },
            dataIndex: 'users',
            key: 'users',
            width: '50%',
        },
    ];
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            <DepartmentModal
                visible={visible}
                setVisible={setVisible}
                emptyFieldsMessage={emptyFieldsMessage}
                getDepartments={refetchUsers}
                successCreatedDepartmentMessage={successCreatedDepartmentMessage}
                errorCreatedDepartmentMessage={errorCreatedDepartmentMessage}
                data={selectedRecordData}
                setData={setSelectedRecordData}
                errorUpdatedDepartmentMessage={errorUpdatedDepartmentMessage}
                successUpdatedDepartmentMessage={successUpdatedDepartmentMessage}
            />
            <GridSettingsModal
                moduleName={'departments'}
                successUpdatedSettings={successUpdatedSettingsMessage}
                visible={visibleSettings}
                setVisible={setVisibleSettings}
                showBorders={showCellBorders}
                showHeader={showHeader}
                setShowHeader={setShowHeader}
                setShowBorders={setShowBorders}
            />
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginLeft: '15px',
                marginRight: '15px',
                marginBottom: '10px'
            }}>
                <div style={{display: 'flex'}}>
                    <Button onClick={() => setVisible(true)}> </Button>
                </div>
                <Tooltip placement={'leftTop'} title={' '}>
                    <Button className='settingsButton' icon={<SettingOutlined/>} shape={'circle'}
                            onClick={() => setVisibleSettings(true)}/>
                </Tooltip>
            </div>
            <Table
                bordered={showCellBorders}
                loading={getUsersIsLoading}
                showHeader={showHeader}
                columns={columns}
                dataSource={data}
                pagination={{position: ["bottomRight"], defaultPageSize: 100}}
                onRow={(record: any) => {
                    return {
                        onDoubleClick: () => {
                            setVisible(true);
                            setSelectedRecordData(record);
                        }
                    }
                }}
            />
        </div>
    );
}