import {Alert, Button, Divider, Input, Modal, Spin, Steps, Tag, Typography} from "antd";
import {DataType} from "../modules/teps/TepModule";
import Dragable, {DraggableData, DraggableEvent} from 'react-draggable';
import React, {useEffect, useRef, useState} from "react";
import {tepsAPI} from "../services/TepService";
import {useAppSelector} from "../hooks/redux";
import {IRole} from "../models/IRole";
import {IStatus} from "../models/IStatus";
import {DeclineTepModal} from "./DeclineTepModal";
import {ConfirmTepModal} from "./ConfirmTepModal";

const {TextArea} = Input;
const {Text} = Typography;

type ModalProps = {
    planYear: string;
    record: DataType,
    visible: boolean,
    setVisible: Function,
    successUpdateTep: Function,
    errorUpdateTep: Function,
    setData: Function,
    selectedRole: string,
    isFillingAvailable: boolean,
    warningEmptyDeclineField: Function,
    warningEmptyChangeExplanationField: Function,
    warningEmptyPlanYearField: Function
}

export const TepModal = (props: ModalProps) => {
    const generalStore = useAppSelector(store => store.userReducer);
    const [updateTep, {data: teps, error, isLoading, isSuccess: isSuccessUpdateTep}] = tepsAPI.useUpdateTepMutation();
    const [getPrevYearValue, {
        data: prevYearValueReq,
        isLoading: getPrevYearValueLoading
    }] = tepsAPI.useGetPrevYearValueMutation();
    const [planYearValue, setPlanYearValue] = useState<string>("");
    const [prevYearValue, setPrevYearValue] = useState<string>("");
    const [changeExplanation, setChangeExplanation] = useState<string>(props.record?.changeExplanation);
    const [declineExplanationZam, setDeclineExplanationZam] = useState<string | undefined>(props.record?.declineExplanationZam);
    const [declineExplanationBoss, setDeclineExplanationBoss] = useState<string | undefined>(props.record?.declineExplanationBoss);
    const [status, setStatus] = useState<string | undefined>(undefined);
    const [selectedRole, setSelectedRole] = useState<IRole | undefined>();
    const [modalWidth,] = useState<number>(970);
    const [declineModal, setDeclineModal] = useState<{ visible: boolean, status: IStatus | undefined, setStatus: Function } | null>(null);
    const [visibleConfirmModal, setVisibleConfirmModal] = useState<boolean>(false);
    useEffect(() => {
        if (props.visible) {
            setPlanYearValue(props.record?.planYearValue);
            setPrevYearValue(props.record?.prevYearValue);
            setChangeExplanation(props.record?.changeExplanation?.trim());
            setStatus(props.record?.status);
        }
    }, [props.visible]);
    useEffect(() => {
        if (props.selectedRole)
            setSelectedRole(generalStore.roles.find((role: IRole) => role.name === props.selectedRole));
    }, [props.selectedRole])
    const [disabled, setDisabled] = useState<boolean>(false);
    const [bounds, setBounds] = useState({left: 0, top: 0, bottom: 0, right: 0});
    const draggableRef = useRef<HTMLDivElement>(null);
    const onStart = (_event: DraggableEvent, uiData: DraggableData) => {
        const {clientWidth, clientHeight} = window.document.documentElement;
        const targetRect = draggableRef.current?.getBoundingClientRect();
        if (!targetRect) {
            return;
        }
        setBounds({
            left: -targetRect.left + uiData.x,
            right: clientWidth - (targetRect.right - uiData.x),
            top: -targetRect.top - uiData.y,
            bottom: clientHeight - (targetRect.bottom - uiData.y),
        })
    }
    const handleUpdateTep = (status: IStatus | undefined, declineExplanation?: string) => {
        if  (planYearValue === null || planYearValue?.toString().trim().length === 0)
            props.warningEmptyPlanYearField();
        else if (prevYearValue != planYearValue && (!changeExplanation || changeExplanation?.toString().trim()?.length===0)){
            props.warningEmptyChangeExplanationField();
        }
        else {
            let tep: any = {
                id: props.record.id,
                plan_year_value: planYearValue ?? '',
                prev_year_value: prevYearValue ?? '',
                statusId: status ? status.id : null,
                change_explanation: prevYearValue != planYearValue ? changeExplanation ?? '' : '',
                decline_explanation_zam: (declineExplanation !== undefined && selectedRole?.code === 3) ? declineExplanation : props.record.declineExplanationZam,
                decline_explanation_boss: (declineExplanation !== undefined && selectedRole?.code === 4) ? declineExplanation : props.record.declineExplanationBoss,
            };
            updateTep(tep);
        }
    }
    useEffect(() => {
        if (error)
            props.errorUpdateTep();
    }, [error])
    useEffect(() => {
        if (teps) {
            props.successUpdateTep();
            props.setData((prev: DataType[]) => {
                return prev.map((record: DataType) => {
                    if (record?.id === props?.record?.id)
                        return {
                            ...record,
                            changeExplanation: prevYearValue != planYearValue ? changeExplanation ?? '' : '',
                            declineExplanationZam: declineExplanationZam ?? props.record.declineExplanationZam,
                            declineExplanationBoss: declineExplanationBoss ?? props.record.declineExplanationBoss,
                            planYearValue,
                            prevYearValue,
                            status: status ?? record.status
                        }
                    else return record;
                });
            });
            props.setVisible(false);
        }
    }, [teps])
    useEffect(() => {
        if (props.visible) {
            getPrevYearValue({normfactor: props.record.nfName, filial: props.record.filial, year: props.planYear});
        }
    }, [props.visible])
    useEffect(() => {
        if (prevYearValueReq) {
            setPrevYearValue(prevYearValueReq.value);
        }
    }, [prevYearValueReq])
    if (props.record)
        return (
            <Modal
                title={props.record.nfName}
                footer={[]}
                centered={true}
                open={props.visible}
                onCancel={() => props.setVisible(false)}
                modalRender={(modal) => (
                    // @ts-ignore
                    <Dragable
                        disabled={disabled}
                        bounds={bounds}
                        onStart={(event, uiData) => onStart(event, uiData)}
                    >
                        <div ref={draggableRef}>{modal}</div>
                    </Dragable>
                )}
                width={modalWidth}
            >
                <DeclineTepModal handleUpdateTep={handleUpdateTep} declineModal={declineModal}
                                 setDeclineModal={setDeclineModal}
                                 warningEmptyDeclineField={props.warningEmptyDeclineField}
                                 setDeclineExplanationZam={setDeclineExplanationZam}
                                 setDeclineExplanationBoss={setDeclineExplanationBoss}
                />
                <ConfirmTepModal handleUpdateTep={handleUpdateTep} visible={visibleConfirmModal}
                                 setVisible={setVisibleConfirmModal} setStatus={setStatus}
                                 setDeclineExplanationZam={setDeclineExplanationZam}
                                 declineExplanationZam={props.record.declineExplanationZam}/>
                {isLoading ?
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: 400}}>
                        <Spin size="large"/>
                    </div> :
                    <div style={{
                        display: 'flex',
                        flexDirection: "column",
                        justifyContent: 'center',
                        alignItems: 'center'
                    }}>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 25,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Input value={props.record.responsibilityStructure} disabled={true}/>
                        </div>
                        <div style={{display: 'flex', alignItems: 'center', width: '100%', marginBottom: 15}}
                             onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}>
                            <div style={{
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'start',
                                fontSize: 14
                            }}>
                                <Text style={{marginBottom: 5}}> </Text>
                                <Tag style={{maxWidth: 270, overflow: 'hidden'}} color={
                                    props.record.status === '' ? 'default' :
                                        props.record.status === ' ' ? 'purple' :
                                            props.record.status === '' ? 'success' :
                                                props.record.status === '  ' ? 'orange' :
                                                    props.record.status === ' ' ? 'processing' :
                                                        props.record.status === '      ' ? 'red' :
                                                            props.record.status === '     ' ? 'magenta' :
                                                                'blue'
                                }>{props.record.status}</Tag>
                            </div>
                            <Divider type={'vertical'} style={{height: 60}}/>
                            <Steps
                                style={{display: 'flex', marginTop: 15, marginLeft: -25}}
                                progressDot
                                current={
                                    status === '' ? 0 :
                                        status === ' ' ? 1 :
                                            status === '  ' ? 2 :
                                                status === ' ' ? 3 :
                                                    status === '' ? 4 :
                                                        status === '     ' ? 1 :
                                                            status === '      ' ? 3 : 0
                                }
                                direction={'horizontal'}
                            >
                                <Steps.Step title={''}/>
                                <Steps.Step title={' '}/>
                                <Steps.Step title={'  '}/>
                                <Steps.Step title={' '}/>
                                <Steps.Step title={''}/>
                            </Steps>
                        </div>
                        {props.record.declineExplanationZam?.length > 0 &&
                            <div
                                onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                                style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'start',
                                    width: '100%',
                                    marginBottom: 15,
                                    fontSize: 14
                                }}>
                                <Text style={{marginBottom: 5}}>    
                                    </Text>
                                <Text style={{
                                    marginBottom: 5,
                                    fontWeight: 'bold'
                                }}>{props.record.declineExplanationZam}</Text>
                            </div>
                        }
                        {props.record.declineExplanationBoss?.length > 0 &&
                            <div
                                onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                                style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'start',
                                    width: '100%',
                                    marginBottom: 15,
                                    fontSize: 14
                                }}>
                                <Text style={{marginBottom: 5}}>    
                                    
                                    </Text>
                                <Text style={{
                                    marginBottom: 5,
                                    fontWeight: 'bold'
                                }}>{props.record.declineExplanationBoss}</Text>
                            </div>
                        }
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}> </Text>
                            <Input value={props.planYear} disabled={true}/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}>   </Text>
                            <Input onChange={(e) => setPlanYearValue(e.target.value)}
                                   disabled={props.record.status === ""
                                       || props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 1)?.name //  
                                       || (status === generalStore?.statuses?.find((status: IStatus) => status.code === "2")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //           
                                       || (status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //          
                                       || (status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 3)?.name) //           
                                       || (props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "6")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //               
                            }
                                   value={planYearValue} allowClear/>
                        </div>
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)} style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'start',
                            width: '100%',
                            marginBottom: 15,
                            fontSize: 14
                        }}>
                            <Text style={{marginBottom: 5}}>   </Text>
                            <Input onChange={(e) => setPrevYearValue(e.target.value)}
                                   disabled={true}
                                   value={prevYearValue}
                                   allowClear/>
                        </div>
                        {prevYearValue != planYearValue ?
                            <div
                                onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                                style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'start',
                                    width: '100%',
                                    marginBottom: 15,
                                    fontSize: 14
                                }}>
                                <Text style={{marginBottom: 5}}> </Text>
                                <TextArea
                                    disabled={props.record.status === ""
                                        || props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 1)?.name //  
                                        || (status === generalStore?.statuses?.find((status: IStatus) => status.code === "2")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //           
                                        || (status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //          
                                        || (status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 3)?.name) //           
                                        || (props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "6")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //               
                                }
                                    onChange={(e) => setChangeExplanation(e.target.value)}
                                    allowClear
                                    value={changeExplanation}/>
                            </div>
                            :
                            <div
                                onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                                style={{
                                    display: 'flex',
                                    justifyContent: 'center',
                                    width: '100%',
                                    marginBottom: 15,
                                    fontSize: 14
                                }}>
                                <Alert style={{width: '100%'}} showIcon
                                       message={'   ,  '} type={'info'}/>
                            </div>
                        }
                        <div onMouseOver={() => setDisabled(true)} onMouseLeave={() => setDisabled(false)}
                             style={{display: 'flex', justifyContent: 'space-evenly', width: '100%', fontSize: 14}}>

                            {(!(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 1)?.name) //    
                                    && !(props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "4")?.name)) //     
                                &&
                                <Button
                                    disabled={(props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "2")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //           
                                        || (props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //          
                                        || (props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 3)?.name) //           
                                        || (props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "6")?.name && props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name) //               
                                }
                                    onClick={() => {
                                        handleUpdateTep(undefined);
                                    }}>
                                    
                                </Button>}

                            {(!(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 1)?.name)//    
                                    && !(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 3)?.name)//       
                                    && !(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 4)?.name)//      
                                    && !(props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "4")?.name) //    
                                ) &&
                                <Button
                                    disabled={props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "2")?.name //     
                                        || props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name //    
                                        || props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "4")?.name //   
                                        || props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "6")?.name //         
                                    }
                                    onClick={() => {
                                        handleUpdateTep(generalStore?.statuses?.find((status: IStatus) => status.code === "2"));
                                        setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "2")?.name)
                                    }}>
                                      
                                </Button>}

                            {(!(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 1)?.name)//    
                                    && !(props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "4")?.name) //    
                                    && !(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 2)?.name)//      
                                    && !(props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 4)?.name)//      
                                ) &&
                                <>
                                    <Button
                                        disabled={props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name} //    
                                        onClick={() => {
                                            if (props.record.declineExplanationZam)
                                                if (props.record.declineExplanationZam.trim().length === 0) {
                                                    handleUpdateTep(generalStore?.statuses?.find((status: IStatus) => status.code === "3"));
                                                    setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name)
                                                } else
                                                    setVisibleConfirmModal(true)
                                            else {
                                                handleUpdateTep(generalStore?.statuses?.find((status: IStatus) => status.code === "3"));
                                                setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name)
                                            }
                                        }}>
                                         
                                    </Button>
                                    <Button
                                        disabled={(props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "3")?.name) //    
                                            || props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "5")?.name //     
                                        }
                                        onClick={() => {
                                            setDeclineModal({
                                                visible: true,
                                                status: generalStore?.statuses?.find((status: IStatus) => status.code === "5"),
                                                setStatus,
                                            })
                                        }}>
                                        
                                    </Button>
                                </>
                            }

                            {props.selectedRole === generalStore?.roles?.find((role: IRole) => role.code === 4)?.name //      
                                &&
                                <>
                                    <Button
                                        disabled={props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "4")?.name} //   
                                        onClick={() => {
                                            handleUpdateTep(generalStore?.statuses?.find((status: IStatus) => status.code === "4"));
                                            setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "4")?.name)
                                        }}>
                                        
                                    </Button>
                                    <Button
                                        disabled={props.record.status === generalStore?.statuses?.find((status: IStatus) => status.code === "6")?.name} //     
                                        onClick={() => {
                                            setDeclineModal({
                                                visible: true,
                                                status: generalStore?.statuses?.find((status: IStatus) => status.code === "6"),
                                                setStatus,
                                            })
                                            //setStatus(generalStore?.statuses?.find((status: IStatus) => status.code === "6")?.name)
                                        }}>
                                        
                                    </Button>
                                </>
                            }

                            <Button onClick={() => props.setVisible(false)}></Button>
                        </div>
                    </div>
                }
            </Modal>)
    else
        return <></>
}