import React, {useEffect, useState} from 'react';
import {Button, message, Table, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {SettingOutlined} from "@ant-design/icons";
import "../../App.css";
import {dictAPI} from "../../services/DictService";
import {INormfactor} from "../../models/INormfactor";
import {GridSettingsModal} from "../../components/GridSettingsModal";
import {NormfactorModal} from "../../components/NormfactorModal";

const {Text} = Typography;

export interface DataType {
    id: number;
    key: React.Key;
    name: string;
    dimension: string;
    source: string;
    responsibilityStructure: string;
    number: number;
}

export const NormfactorModule = (props: any) => {
    // Create Toasts context
    const [messageApi, contextHolder] = message.useMessage();
    const successUpdatedSettings = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successLoadNormfactors = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorLoadTDicts = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const emptyFieldsWarning = () => {
        messageApi.open({
            type: 'warning',
            content: '   ',
            duration: 1,
        });
    }
    const successCreatedNormfactorMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successUpdatedNormfactorMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorCreatedNormfactorMessage = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const errorUpdatedNormfactorMessage = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    //End of toasts
    // Requests
    const [getNormfactors, {
        data: normfactors,
        error: getNormfactorsRequestError,
        isLoading: getNormfactorsRequestIsLoading
    }] = dictAPI.useGetDictsNormfactorsMutation();
    // End of requests
    const [data, setData] = useState<DataType[]>();
    const [visibleSettings, setVisibleSettings] = useState<boolean>(false);
    const [visible, setVisible] = useState<boolean>(false);
    const [showHeader, setShowHeader] = useState<boolean>(localStorage.getItem('tableHeader-normfactors') ? localStorage.getItem('tableHeader-normfactors') === 'true' : true);
    const [showCellBorders, setShowBorders] = useState<boolean>(localStorage.getItem('tableBorders-normfactors') ? localStorage.getItem('tableBorders-normfactors') === 'true' : true);
    const [previewText, setPreviewText] = useState<boolean>(localStorage.getItem('tablePreviewText-normfactors') ? localStorage.getItem('tablePreviewText-normfactors') === 'true' : true);
    const [selectedRecordData, setSelectedRecordData] = useState();
    const [titleFontSize, ] = useState('13px');
    const [tableCellFontSize, ] = useState('15px');
    useEffect(() => {
        getNormfactors(props.dictId)
    }, []);
    useEffect(() => {
        if (normfactors) {
            setData(normfactors.normfactors.map((nf: INormfactor, key: number) => ({...nf, key} as DataType)));
            successLoadNormfactors();
        }
    }, [normfactors]);
    useEffect(() => {
        if (getNormfactorsRequestError) {
            errorLoadTDicts();
        }
    }, [getNormfactorsRequestError]);
    useEffect(()=>{if (!visible) setSelectedRecordData(undefined)}, [visible])
    const columns: ColumnsType<DataType> = [
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}> </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: '67px',
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: '67px'}}>{text}</div>
                };
            },
            dataIndex: 'number',
            key: 'number',
            width: 'tableCellFontSize%',
        },
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                     
                </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: '67px',
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: '67px'}}>
                        {(previewText && text?.length > 70) ?
                            <Tooltip placement={'left'} title={text}>
                                {text}
                            </Tooltip> :
                            <div>{text}</div>
                        }
                    </div>
                };
            },
            dataIndex: 'name',
            key: 'name',
            width: '30%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                    </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: '67px',
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: '67px'}}>
                        {(previewText && text?.length > 70) ?
                            <Tooltip placement={'left'} title={text}>
                                {text}
                            </Tooltip> :
                            <div>{text}</div>
                        }
                    </div>
                };
            },
            dataIndex: 'dimension',
            key: 'dimension',
            width: '10%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                     </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: '67px',
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: '67px'}}>
                        {(previewText && text?.length > 70) ?
                            <Tooltip placement={'left'} title={text}>
                                {text}
                            </Tooltip> :
                            <div>{text}</div>
                        }
                    </div>
                };
            },
            dataIndex: 'source',
            key: 'source',
            width: '30%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                    </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: '67px',
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: '67px'}}>{text}</div>
                };
            },
            dataIndex: 'responsibility_structure',
            key: 'responsibility_structure',
            width: '15%',
        },
    ];
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            {normfactors &&
                <NormfactorModal
                    visible={visible}
                    setVisible={setVisible}
                    dictId={props.dictId}
                    emptyFieldsWarning={emptyFieldsWarning}
                    getNormfactors={getNormfactors}
                    successCreatedNormfactorMessage={successCreatedNormfactorMessage}
                    errorCreatedNormfactorMessage={errorCreatedNormfactorMessage}
                    responsibilityStructures={normfactors.responsibilityStructure}
                    data={selectedRecordData}
                    setData={setSelectedRecordData}
                    errorUpdatedNormfactorMessage={errorUpdatedNormfactorMessage}
                    successUpdatedNormfactorMessage={successUpdatedNormfactorMessage}/>
            }
            <GridSettingsModal
                moduleName={'normfactors'}
                successUpdatedSettings={successUpdatedSettings}
                previewText={previewText}
                setPreviewText={setPreviewText}
                visible={visibleSettings}
                setVisible={setVisibleSettings}
                showBorders={showCellBorders}
                showHeader={showHeader}
                setShowHeader={setShowHeader}
                setShowBorders={setShowBorders}
            />
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginLeft: '15px',
                marginRight: '15px',
                marginBottom: '10px'
            }}>
                <div style={{display: 'flex'}}>
                    <Button onClick={() => setVisible (true)}> </Button>
                </div>
                <Tooltip placement={'leftTop'} title={' '}>
                    <Button className='settingsButton' icon={<SettingOutlined/>} shape={'circle'}
                            onClick={() => setVisibleSettings(true)}/>
                </Tooltip>
            </div>
            <Table
                bordered={showCellBorders}
                loading={getNormfactorsRequestIsLoading}
                showHeader={showHeader}
                columns={columns}
                dataSource={data}
                pagination={{position: ["bottomRight"], defaultPageSize: 10}}
                onRow={(record: any) => {
                    return {
                        onDoubleClick: () => {
                            setSelectedRecordData(record);
                            setVisible(true);
                        }
                    }
                }}
            />
        </div>
    );
}