import React, {useEffect, useState} from 'react';
import {Button, message, Table, Tag, Tooltip, Typography} from 'antd';
import type {ColumnsType} from 'antd/es/table';
import {blue} from '@ant-design/colors';
import {SettingOutlined} from "@ant-design/icons";
import {CellSize} from "../../ENUMS";
// @ts-ignore
import Highlighter from "react-highlight-words"
import "../../App.css";
import {IUser} from "../../models/IUser";
import {IRole} from "../../models/IRole";
import {IFilial} from "../../models/IFilial";
import {userAPI} from "../../services/UserService";
import {GridSettingsModal} from "../../components/GridSettingsModal";
import {UserModal} from "../../components/UserModal";

const {Text} = Typography;

interface DataType extends IUser {
    key: React.Key;
}
interface ColumnItem {
    text: string;
    value: string;
}

const getUniquesList = (data: DataType[] | undefined, column : string): ColumnItem[] => {
    if (data) {
        let result: ColumnItem[] = []
        data.forEach((record: any) => {
            if (!result.find((resRecord: any) => (resRecord.text === record[column]))) {
                result.push({
                    text: record[column],
                    value: record[column]
                })
            }
        })
        return result
    } else return [];
}

export const UserModule: React.FC = () => {
    // Create Toasts context
    const [messageApi, contextHolder] = message.useMessage();
    const successUpdatedSettings = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const successLoadUsers = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorLoadUsers = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const successUpdateUserMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    const errorUpdateUserMessage = () => {
        messageApi.open({
            type: 'error',
            content: ' ',
            duration: 1,
        });
    }
    const emptyFieldsMessage = () => {
        messageApi.open({
            type: 'warning',
            content: '   ',
            duration: 1,
        });
    }
    const errorCreatedUserMessage = () => {
        messageApi.open({
            type: 'error',
            content: '  ,    ',
            duration: 1,
        });
    }
    const successCreatedUserMessage = () => {
        messageApi.open({
            type: 'success',
            content: ' ',
            duration: 1,
        });
    }
    //End of toasts
    // Requests
    const {
        data: users,
        error: getUsersError,
        isLoading: getUsersIsLoading,
        refetch: refetchUsers
    } = userAPI.useGetUsersQuery();
    // End of requests
    const [data, setData] = useState<DataType[]>();
    const [visibleSettings, setVisibleSettings] = useState<boolean>(false);
    const [visible, setVisible] = useState<boolean>(false);
    const [showHeader, setShowHeader] = useState<boolean>(localStorage.getItem('showHeaderUsersGrid') ? localStorage.getItem('showHeaderUsersGrid') === 'true' : true);
    const [showCellBorders, setShowBorders] = useState<boolean>(localStorage.getItem('showHeaderUsersGrid') ? localStorage.getItem('showBordersUsersGrid') === 'true' : true);
    const [rowHeight, setRowHeight] = useState<string>('100px');
    const [selectedRecordData, setSelectedRecordData] = useState();
    const [titleFontSize,] = useState('13px');
    const [tableCellFontSize,] = useState('15px');
    useEffect(() => {
        if (users) {
            setData(users.map((user: IUser, key: number) => ({...user, key} as DataType)));
            successLoadUsers();
        }
    }, [users]);
    useEffect(() => {
        if (getUsersError) {
            errorLoadUsers();
        }
    }, [getUsersError]);
    useEffect(() => {
        refetchUsers();
    }, []);
    const columns: ColumnsType<DataType> = [
        {
            title: () => {
                return <Text
                    style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                    </Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'username',
            key: 'username',
            width: '8%',
            filters: getUniquesList(data, 'username'),
            onFilter: (value: any, record: any) => {
                return record.username.indexOf(value) === 0
            },
            sorter: (a: any, b: any) => a?.username?.length - b?.username?.length,
            sortDirections: ['descend', 'ascend'],
            filterSearch: true,
        },
        {
            title: () => {
                return <Text
                    style={{
                        color: blue.primary,
                        fontSize: titleFontSize,
                        maxHeight: 100,
                        overflow: 'hidden'
                    }}></Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'surname',
            key: 'surname',
            width: '8%',
            filters: getUniquesList(data, 'surname'),
            onFilter: (value: any, record: any) => {
                return record.surname.indexOf(value) === 0
            },
            sorter: (a: any, b: any) => a?.surname?.length - b?.surname?.length,
            sortDirections: ['descend', 'ascend'],
            filterSearch: true,
        },
        {
            title: () => {
                return <Text
                    style={{
                        color: blue.primary,
                        fontSize: titleFontSize,
                        maxHeight: 100,
                        overflow: 'hidden'
                    }}></Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'name',
            key: 'name',
            width: '8%',
            filters: getUniquesList(data, 'name'),
            onFilter: (value: any, record: any) => {
                return record.name.indexOf(value) === 0
            },
            sorter: (a: any, b: any) => a?.name?.length - b?.name?.length,
            sortDirections: ['descend', 'ascend'],
            filterSearch: true,
        },
        {
            title: () => {
                return <Text
                    style={{
                        color: blue.primary,
                        fontSize: titleFontSize,
                        maxHeight: 100,
                        overflow: 'hidden'
                    }}></Text>
            },
            render(text, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={text?.length > 70 ? {
                        fontSize: tableCellFontSize,
                        color: 'black',
                        height: rowHeight,
                        overflowY: 'scroll'
                    } : {fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        <div>{text}</div>
                    </div>
                };
            },
            dataIndex: 'secondName',
            key: 'secondName',
            width: '8%',
            filters: getUniquesList(data, 'secondName'),
            onFilter: (value: any, record: any) => {
                return record.secondName.indexOf(value) === 0
            },
            sorter: (a: any, b: any) => a?.secondName?.length - b?.secondName?.length,
            sortDirections: ['descend', 'ascend'],
            filterSearch: true,
        },
        {
            title: () => {
                return <Text
                    style={{
                        color: blue.primary,
                        fontSize: titleFontSize,
                        maxHeight: 100,
                        overflow: 'hidden'
                    }}></Text>
            },
            render(roles, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={{fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        {roles.map((role: IRole) => <Tag color='geekblue' style={{
                            marginBottom: 3,
                            maxWidth: 250,
                            overflow: 'hidden'
                        }}>{role.name}</Tag>)}
                    </div>
                };
            },
            dataIndex: 'roles',
            key: 'roles',
            width: '20%',
        },
        {
            title: () => {
                return <Text
                    style={{
                        color: blue.primary,
                        fontSize: titleFontSize,
                        maxHeight: 100,
                        overflow: 'hidden'
                    }}></Text>
            },
            render(filials, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={{fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        {filials.map((filial: IFilial) => <Tag color='geekblue'
                                                               style={{marginBottom: 3}}>{filial.name}</Tag>)}
                    </div>
                };
            },
            dataIndex: 'filials',
            key: 'filials',
            width: '25%',
        },
        {
            title: () => {
                return <Text style={{color: blue.primary, fontSize: titleFontSize, maxHeight: 100, overflow: 'hidden'}}>
                    </Text>
            },
            render(department, record) {
                return {
                    props: {
                        style: {color: 'red'}
                    },
                    children: <div style={{fontSize: tableCellFontSize, color: 'black', height: rowHeight}}>
                        {department ? <Tag color='geekblue' style={{
                            marginBottom: 3,
                            maxWidth: 260,
                            overflow: 'hidden'
                        }}>{department.name}</Tag> : ''}
                    </div>
                };
            },
            key: 'department',
            dataIndex: 'department',
            width: '20%',
        },
    ];
    return (
        <div style={{height: window.innerHeight - 140, paddingTop: '10px'}}>
            {contextHolder}
            <UserModal
                data={selectedRecordData}
                setData={setSelectedRecordData}
                visible={visible}
                setVisible={setVisible}
                emptyFieldsMessage={emptyFieldsMessage}
                getUsers={refetchUsers}
                successUpdateUserMessage={successUpdateUserMessage}
                errorUpdateUserMessage={errorUpdateUserMessage}
                errorCreatedUserMessage={errorCreatedUserMessage}
                successCreatedUserMessage={successCreatedUserMessage}
            />
            <GridSettingsModal
                moduleName={'users'}
                successUpdatedSettings={successUpdatedSettings}
                visible={visibleSettings}
                setVisible={setVisibleSettings}
                showBorders={showCellBorders}
                showHeader={showHeader}
                setShowHeader={setShowHeader}
                setShowBorders={setShowBorders}
            />
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginLeft: '15px',
                marginRight: '15px',
                marginBottom: '10px'
            }}>
                <div style={{display: 'flex'}}>
                    <Button onClick={() => setVisible(true)}> </Button>
                </div>
                <Tooltip placement={'leftTop'} title={' '}>
                    <Button className='settingsButton' icon={<SettingOutlined/>} shape={'circle'}
                            onClick={() => setVisibleSettings(true)}/>
                </Tooltip>
            </div>
            <Table
                bordered={showCellBorders}
                loading={getUsersIsLoading}
                showHeader={showHeader}
                columns={columns}
                dataSource={data}
                pagination={{position: ["bottomRight"], defaultPageSize: 10}}
                onRow={(record: any) => {
                    return {
                        onDoubleClick: () => {
                            setVisible(true);
                            setSelectedRecordData(record);
                        }
                    }
                }}
            />
        </div>
    );
}