import {combineReducers, configureStore} from "@reduxjs/toolkit";
import userReducer from './reducers/GeneralSlice'
import {userAPI} from "../services/UserService";
import {tepsAPI} from "../services/TepService";
import {dictAPI} from "../services/DictService";
import {departmentAPI} from "../services/DepartmentService";
import {reportAPI} from "../services/ReportService";
import {filialAPI} from "../services/FilialService";
import {normfactorAPI} from "../services/NormfactorService";
import {settingsAPI} from "../services/SettingsService";
import {statusAPI} from "../services/StatusService";
import {roleAPI} from "../services/RoleService";

const rootReducer = combineReducers({
    userReducer,
    [userAPI.reducerPath]: userAPI.reducer,
    [tepsAPI.reducerPath]: tepsAPI.reducer,
    [dictAPI.reducerPath]: dictAPI.reducer,
    [departmentAPI.reducerPath]: departmentAPI.reducer,
    [reportAPI.reducerPath]: reportAPI.reducer,
    [filialAPI.reducerPath]: filialAPI.reducer,
    [normfactorAPI.reducerPath]: normfactorAPI.reducer,
    [settingsAPI.reducerPath]: settingsAPI.reducer,
    [statusAPI.reducerPath]: statusAPI.reducer,
    [roleAPI.reducerPath]: roleAPI.reducer,
})

export const setupStore = () => {
    return configureStore({
        reducer: rootReducer,
        middleware: (getDefaultMiddleware) =>
            getDefaultMiddleware()
                .concat(userAPI.middleware)
                .concat(tepsAPI.middleware)
                .concat(dictAPI.middleware)
                .concat(departmentAPI.middleware)
                .concat(reportAPI.middleware)
                .concat(filialAPI.middleware)
                .concat(normfactorAPI.middleware)
                .concat(settingsAPI.middleware)
                .concat(statusAPI.middleware)
                .concat(roleAPI.middleware)
    })
}

export type RootState = ReturnType<typeof rootReducer>
export type AppStore = ReturnType<typeof setupStore>
export type AppDispatch = AppStore['dispatch']
