interface DictsListItem {
    text: string;
    value: string;
}

interface Grouped {
    isGrouped: boolean;
    column: GroupedColumnTepsGrid;
}

export interface DataType extends ITep {
    key: React.Key;
    children?: any;
}

type DataIndex = keyof DataType;

function getUniqDictsList(data: DataType[] | undefined): DictsListItem[] {
    if (data) {
        let result: DictsListItem[] = []
        data.forEach((record: any) => {
            if (!result.find((resRecord: any) => (resRecord.text === record.shortDict))) {
                result.push({
                    text: record.shortDict,
                    value: record.shortDict
                })
            }
        })
        return result
    } else return [];
}

