const getGroupedData = (data: DataType[] | undefined, column: GroupedColumnTepsGrid) => {
    if (data && column === GroupedColumnTepsGrid.DICT) {
        return getUniqDictsList(data).map((dict, key) => {
            let parentKey = key + 1
            let result: any = {
                key: parentKey,
                shortDict: dict.text,
                children: [],
            }
            let counter = 0
            data.forEach((record: any) => {
                if (record.shortDict === dict.text) {
                    counter += 1
                    result.children.push({...record, shortDict: '', key: parseInt(`${parentKey}${counter}`)})
                }
            })
            return result
        })
    }
    if (data && column === GroupedColumnTepsGrid.NF) {
        return getUniqNfNameList(data).map((nf, key) => {
            let parentKey = key + 1
            let result: any = {
                key: parentKey,
                nfName: nf.text,
                children: [],
            }
            let counter = 0
            data.forEach((record: any) => {
                if (record.nfName === nf.text) {
                    counter += 1
                    result.children.push({...record, nfName: '', key: parseInt(`${parentKey}${counter}`)})
                }
            })
            return result
        })
    }
    if (data && column === GroupedColumnTepsGrid.STATUS) {
        return getUniqStatusList(data).map((status, key) => {
            let parentKey = key + 1
            let result: any = {
                key: parentKey,
                status: status.text,
                children: [],
            }
            let counter = 0
            data.forEach((record: any) => {
                if (record.status === status.text) {
                    counter += 1
                    result.children.push({...record, status: '', key: parseInt(`${parentKey}${counter}`)})
                }
            })
            return result
        })
    }
    if (data && column === GroupedColumnTepsGrid.RESP_STRUCT) {
        return getUniqResponsibilityStructureList(data).map((responsibilityStructure, key) => {
            let parentKey = key + 1
            let result: any = {
                key: parentKey,
                responsibilityStructure: responsibilityStructure.text,
                children: [],
            }
            let counter = 0
            data.forEach((record: any) => {
                if (record.responsibilityStructure === responsibilityStructure.text) {
                    counter += 1
                    result.children.push({
                        ...record,
                        responsibilityStructure: '',
                        key: parseInt(`${parentKey}${counter}`)
                    })
                }
            })
            return result
        })
    }
}

const getGroupedColumns = (columns: any, col: GroupedColumnTepsGrid) => {
    let first = columns.find((column: any) => column.key == col);
    let other = columns.filter((column: any) => !(column.key === col));
    other.unshift(first);
    return other;
}
